import { reqGetAuditBug, reqGetJDBC, reqGetMybatis, reqGetUpload, reqGetCross, reqGetSSRF, reqGetXEE, reqGetRCE, reqPostlogout, reqPostHistory } from "../../api/index";
const state = {
    auditBug: [],
    jdbc: [],
    mybatis: [],
    upload: [],
    traversal: [],
    SSRF: [],
    XEE: [],
    RCE: [],
    isComplete: [false, false, false, false, false, false, false],
    isAuditing: false,
};
const mutations = {
    AUDITBUG(state, auditBug) {
        state.auditBug = auditBug;
    },
    JDBC(state, jdbc) {
        state.jdbc = jdbc;
    },
    MYBATIS(state, mybatis) {
        state.mybatis = mybatis;
    },
    UPLOAD(state, upload) {
        state.upload = upload;
    },
    CROSS(state, cross) {
        state.upload = cross;
    },
    SSRF(state, SSRF) {
        state.SSRF = SSRF;
    },
    XEE(state, XEE) {
        state.XEE = XEE;
    },
    RCE(state, RCE) {
        state.RCE = RCE;
    },
    ISCOMPLETED(state, data) {
        state.isComplete[data.index] = data.status;
    },
    ISAUDITING(state, result) {
        state.isAuditing = result;
    }
};
//获取 漏洞原理、漏洞解决方案数据
const actions = {
    async auditBug({ commit }) {
        let result = await reqGetAuditBug();
        if (result.code == 200)
            commit("AUDITBUG", result.data);
    },
    //用户上传文件 并获得审计数据
    async audit({ commit }, data) {
        let temp = new Object();
        temp.status = false;
        temp.index = data.index;
        commit("ISAUDITING", true);
        let result;
        let rsl = new Object();
        let result2;
        switch (data.index) {
            case 0:
                result = await reqGetJDBC(data.file);
                rsl.type = "SQL注入/JDBC"
                commit("JDBC", result);
                break;
            case 1:
                result = await reqGetMybatis(data.file);
                rsl.type = "SQL注入/Mybatis"
                commit("MYBATIS", result);
                break;
            case 2:
                result = await reqGetUpload(data.file);
                rsl.type = "文件上传漏洞"
                commit("UPLOAD", result);
                break;
            case 3:
                result = await reqGetCross(data.file);
                rsl.type = "路径穿越漏洞"
                commit("CROSS", result);
                break;
            case 4:
                result = await reqGetSSRF(data.file);
                rsl.type = "SSRF"
                commit("SSRF", result);
                break;
            case 5:
                result = await reqGetXEE(data.file);
                rsl.type = "XEE";
                commit("XEE", result);
                break;
            case 6:
                result = await reqGetRCE(data.file);
                rsl.type = "RCE";
                commit("RCE", result);
                break;
        }
        rsl.result = result.data;
        rsl.remarks = result.msg;
        rsl.color = result.color;
        result2 = await reqPostHistory(JSON.stringify(rsl));
        temp.status = true;
        commit("ISCOMPLETED", temp);
        commit("ISAUDITING", false);
    },
    async logout() {
        let result = await reqPostlogout();
    }
};
const getters = {};

export default {
    state,
    mutations,
    actions,
    getters
}