<template>
  <div class="body">
    <div class="nav">
      <div>
        <img @click="goMain" class="logo1" src="./media/logo.png" />
      </div>
    </div>
    <div class="container" id="container">
      <!-- 注册页面 -->
      <div class="form-container sign-up-container">
        <div class="log-container">
          <h1>注册</h1>
          <input
            type="text"
            name="userName"
            id="userName"
            placeholder="请输入用户名"
            v-model="upname"
          />
          <input
            type="password"
            name="password"
            id="password1"
            placeholder="请输入密码"
            v-model="uppassword1"
          />
          <input
            type="password"
            name="password"
            id="password2"
            placeholder="再次输入确认密码"
            v-model="uppassword2"
          />
          <span><i class="el-icon-s-help"></i>点击验证</span>
          <button @click="logup">注册</button>
        </div>
      </div>

      <!-- 登录页面 -->
      <div class="form-container sign-in-container">
        <div class="log-container">
          <h1>登录</h1>
          <input
            type="text"
            name="email"
            id="email"
            placeholder="请输入账号"
            v-model="inname"
          />
          <span class="explain"></span>
          <input
            type="password"
            name="password"
            id="password3"
            placeholder="请输入密码"
            v-model="inpassword"
          />
          <button @click="login">登录</button>
        </div>
      </div>

      <!-- 覆盖容器 -->
      <div class="overlay-container">
        <div class="overlay">
          <!-- 覆盖左边 -->
          <div class="overlay-penal overlay-left-container">
            <h1>欢迎加入！</h1>
            <p>注册您的账号，保持与我们的联络</p>
            <button class="ghost" id="signIn">登录</button>
          </div>

          <!-- 覆盖右边 -->
          <div class="overlay-penal overlay-right-container">
            <h1>您好！</h1>
            <p>输入您的账号，开启代码审计之旅吧</p>
            <button class="ghost" id="signUp">注册</button>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapState } from "vuex";
export default {
  name: "Login",
  data() {
    return {
      inname: "",
      inpassword: "",
      upname: "",
      uppassword1: "",
      uppassword2: "",
    };
  },
  methods: {
    //注册处理
    async logup() {
      if (!this.upname) alert("请输入想要注册的账号");
      else {
        if (this.uppassword1 == this.uppassword2 && this.uppassword1) {
          if (this.uppassword1.length < 6)
            this.$message({
              message: "密码不小于6位",
              type: "warning",
            });
          else {
            let userinfo = new Object();
            userinfo.username = this.upname;
            userinfo.password = this.uppassword1;
            await this.$store.dispatch("logup", JSON.stringify(userinfo));
            if (this.loguprsl.code == 0) {
              this.$message({
                message: this.loguprsl.msg,
                type: "error",
              });
            } else {
              this.$message({
                message: this.loguprsl.data,
                type: "success",
              });
              document.getElementById("signIn").click();
            }
          }
        } else if (!this.uppassword1)
          this.$message({
            message: "密码不能为空",
            type: "warning",
          });
        else if (this.uppassword1 != this.uppassword2)
          this.$message({
            message: "两次输入的密码不一致",
            type: "warning",
          });
      }
    },
    //用户登录
    async login() {
      if (!this.inname && !this.inpassword)
        this.$message.warning("请输入账号和密码");
      else if (!this.inname && this.inpassword)
        this.$message.warning("请输入账号");
      else if (this.inname && !this.inpassword)
        this.$message.warning("请输入密码");
      else {
        let userinfo = {};
        userinfo.username = this.inname;
        userinfo.password = this.inpassword;
        await this.$store.dispatch("login", userinfo);
        if (this.loginrsl.code == 0)
          this.$message({
            message: this.loginrsl.msg,
            type: "error",
          });
        else {
          window.localStorage.setItem("username", this.inname);
          this.$router.push("/main");
        }
      }
    },
    goMain() {
      this.$router.push("/main");
    },
  },
  computed: {
    ...mapState({
      loguprsl: (state) => state.login.loguprsl,
      loginrsl: (state) => state.login.loginrsl,
    }),
  },
  mounted() {
    //绑定切换登录注册动画
    const container = document.getElementById("container");
    const signUpButton = document.getElementById("signUp");
    const signInButton = document.getElementById("signIn");
    signUpButton.onclick = function () {
      container.classList.add("penal-right-active");
    };
    signInButton.onclick = function () {
      container.classList.remove("penal-right-active");
    };
  },
};
</script>

<style lang="css" scoped>
* {
  box-sizing: border-box;
}

.body {
  width: 100%;
  height: 100%;
  background-color: #010415;
}

button {
  outline: none;
  border: 1px solid rgb(8, 147, 163);
  border-radius: 20px;
  background-color: rgb(3, 147, 163);
  padding: 10px 40px;
  font-size: 12px;
  text-transform: uppercase;
  color: white;
  transition: transform 85ms ease-in;
}
h1 {
  text-transform: capitalize;
  font-size: 30px;
}
p {
  margin: 10px 0 30px;
}
button:active {
  transform: scale(0.95);
}
.ghost {
  background-color: transparent;
  border-color: white;
  color: #0c0c0c;
}

.container {
  position: absolute;
  top: 50%;
  left: 50%;
  margin-left: -384px;
  margin-top: -200px;
  width: 768px;
  max-width: 100%;
  min-height: 480px;
  background-color: white;
  box-shadow: 0 14px 28px rgba(0, 0, 0, 0.25), 0 10px 10px rgba(0, 0, 0, 0.22);
  border-radius: 10px;
  overflow: hidden;
}

.nav {
  width: 100%;
  height: 15%;
  background-color: rgb(39, 40, 44);
}

.nav div {
  width: 100%;
  height: 100%;
  float: left;
}

.nav .logo1 {
  width: 10%;
  height: 100%;
}

.nav .logo1:hover {
  cursor: pointer;
}

.verification {
  width: 50%;
  height: 20%;
  background-color: red;
}

.form-container {
  position: absolute;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: white;
  height: 100%;
  width: 50%;
  padding: 0 40px;
  transition: all 0.65s ease-in-out;
}
.form-container .log-container {
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.form-container input {
  width: 100%;
  height: 40px;
  margin: 8px 0;
  background-color: #eeeeee;
  border: none;
  font-size: 14px;
  padding: 0 20px;
}

.sign-in-container {
  z-index: 10;
}
.sign-up-container {
  z-index: 5;
}

.social-container {
  display: flex;
}
.social-container a {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  border: 1px solid #ddd;
  line-height: 40px;
  text-align: center;
  color: #333;
  margin: 20px 8px;
}
.social-container a:hover {
  background-color: #222f3e;
  color: white;
  border-color: #cbcbcc;
}

.overlay-container {
  position: absolute;
  right: 0;
  width: 50%;
  height: 100%;
  background-color: #0c0c0c;
  overflow: hidden;
  transition: transform 0.65s ease-in-out;
  z-index: 100;
}
.overlay {
  position: absolute;
  display: flex;
  left: -100%;
  width: 200%;
  height: 100%;
  background: linear-gradient(to right, #e9e9e9, #e9e9e9);
}

.overlay-penal {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 50%;
  padding: 0 40px;
  text-align: center;
  color: #0c0c0c;
  font-size: 12px;
}
.overlay-left-container {
  transform: translateX(10%);
  transition: transform 0.65s ease-in-out;
}
.overlay-right-container {
  transform: translateX(0);
  transition: transform 0.65s ease-in-out;
}

.container.penal-right-active .sign-in-container {
  z-index: 5;
  transform: translateX(100%);
}

.container.penal-right-active .sign-up-container {
  z-index: 10;
  transform: translateX(100%);
}

.container.penal-right-active .overlay-container {
  transform: translateX(-100%);
}
.container.penal-right-active .overlay {
  transform: translateX(50%);
}
.container.penal-right-active .overlay-left-container {
  transform: translateX(0);
}
.container.penal-right-active .overlay-right-container {
  transform: translateX(-10%);
}
</style>
