//引入vue
import Vue from 'vue'
//引入路由
import Vuerouter from 'vue-router'
//引入element-ui message组件
import { Message } from 'element-ui'
//使用路由
Vue.use(Vuerouter);
//引入store
import store from '../store'

//重写push方法
let originPush = Vuerouter.prototype.push;
Vuerouter.prototype.push = function (location, resolve, reject) {
    //第一个形参：路由跳转的配置对象（query|params）
    //第二个参数：undefined|箭头函数（成功的回调）
    //第三个参数:undefined|箭头函数（失败的回调）
    if (resolve && reject) {
        //push方法传递第二个参数|第三个参数（箭头函数）
        //originPush：利用call修改上下文，变为(路由组件.$router)这个对象，第二参数：配置对象、第三、第四个参数：成功和失败回调函数
        originPush.call(this, location, resolve, reject);
    } else {
        //push方法没有产地第二个参数|第三个参数
        originPush.call(
            this,
            location,
            () => { },
            () => { }
        );
    }
};

//引入路由组件
import Main from '../pages/Main'
import Audit from '../pages/Audit'
import Info from '../pages/Information'
import Login from '../pages/Login'
import JDBC from '../pages/Learning/JDBC'
import Mybatis from '../pages/Learning/Mybatis'
import Upload from '../pages/Learning/Upload'
import Traversial from '../pages/Learning/Traversal'
import SSRF from '../pages/Learning/SSRF'
import XEE from '../pages/Learning/XEE'
import RCE from '../pages/Learning/RCE'
import XSS from '../pages/Learning/XSS'
import Reverse from '../pages/Learning/Reverse'
//配置路由
let router = new Vuerouter({
    routes: [
        {
            path: '/main',
            component: Main
        },
        {
            path: '/audit',
            component: Audit
        },
        {
            path: '/info',
            component: Info
        },
        {
            path: '/login',
            component: Login
        },
        {
            path: '/learning/JDBC',
            name: 'JDBC',
            component: JDBC,
        },
        {
            path: '/Learning/Mybatis',
            name: 'Mybatis',
            component: Mybatis,
        },
        {
            path: '/Learning/traversial',
            name: 'Traversial',
            component: Traversial,
        },
        {
            path: '/Learning/upload',
            name: 'Upload',
            component: Upload,
        },
        {
            path: '/learning/ssrf',
            name: 'SSRF',
            component: SSRF,
        },
        {
            path: '/learning/rce',
            name: 'RCE',
            component: RCE,
        },
        {
            path: '/learning/xee',
            name: 'XEE',
            component: XEE,
        },
        {
            path: '/learning/xss',
            name: 'XSS',
            component: XSS,
        },
        {
            path: '/learning/reverse',
            name: 'reverse',
            component: Reverse,
        },
        {
            path: '*',
            redirect: '/main'
        }
    ],
})

//注册$message

router.beforeEach((to, from, next) => {
    let isLog = store.state.login.isLog;
    if (to.path == '/login' || to.path == '/audit' || to.path == '/info') {
        if (isLog) {
            if (to.path == '/login')
                Message.warning("您已登录了，若想更换账号请先注销");
            else
                next();
        } else {
            if (to.path == "/audit" || to.path == '/info')
                Message.warning("请先登录！");
            else
                next();
        }
    } else
        next();

})

export default router;
