<script>
export default {
  name: "Traversal",
  data() {
    return {
      num: "",
      code1: `GetMapping("/download")public String download(String filename, HttpServletRequest request,\n HttpServletResponse response) {    String filePath = System.getProperty("user.dir") + "/logs/" + \nfilename;    try {        File file = new File(filePath);        InputStream is = new \nBufferedInputStream(new FileInputStream(file));        byte[] buffer = new byte[is.available()];\n        fis.read(buffer);        fis.close();        response.reset();        response.addHeader("Content-Disposition", "attachment;filename=" + filename);        response.addHeader("Content-Length", "" +\n file.length());        OutputStream toClient = new \nBufferedOutputStream(response.getOutputStream());\nresponse.setContentType("application/octet-stream");        toClient.write(buffer);        toClient.flush(); \n       toClient.close();        return "下载文件成功：" + filePath;\n`,
      code2: `public static boolean check_traversal(String content) {    return content.contains("..") || \ncontent.contains("/");}`,
    };
  },
  methods: {
    //textarea自动显示行数
    handleTextareaInput() {
      var target = document.getElementsByClassName("codeshow");
      for (let i = 0; i < target.length; i++) {
        var str = target[i].value;
        str = str.replace(/\r/gi, "");
        str = str.split("\n");
        var n = str.length;
        var lineobj = document.getElementsByClassName("rowshow")[i];
        for (var j = 1; j <= n; j++) {
          if (document.all) {
            this.num += j + "\r\n"; //判断浏览器是否是IE
          } else {
            this.num += j + "\n";
          }
        }
        lineobj.value = this.num;
        this.num = "";
      }
    },
    //重写a标签锚点跳转
    jump(target) {
      let el = document.querySelector(".el-main");
      let tar = document.querySelector(target);
      el.scrollTop = tar.offsetTop;
    },
  },
  mounted() {
    //textarea自动显示行数
    this.handleTextareaInput();
  },
};
</script>

