<template>
  <!-- 代码审计页面 -->
  <el-container style="height: 100%" direction="horizontal">
    <!-- 用户信息 及操作按钮 -->
    <el-aside width="75px">
      <!-- 用户头像 -->
      <el-avatar class="user" :size="57" :src="url"></el-avatar>
      <!-- 个人信息 (用户审计历史)-->
      <i class="el-icon-user" size="20" @click="goInfo" title="个人信息"></i>
      <!-- 文件上传 -->
      <i
        class="el-icon-upload"
        size="20"
        title="上传代码"
        @click="drawer = true"
      ></i>
      <el-drawer
        title="手动输入代码片段/上传java文件"
        :visible.sync="drawer"
        direction="rtl"
        class="codeupload"
      >
        <span>
          <div style="border: 1px solid #202021; margin-left: 10px">
            <textarea
              id="rowshowupload"
              cols="2"
              rows="33"
              wrap="off"
              disabled
            ></textarea>
            <textarea
              id="uploadcode"
              cols="60"
              rows="33"
              onscroll="document.getElementsByClassName('rowshow')[0].scrollTop = this.scrollTop;"
              v-model="code"
              @keydown="tabFunc"
              @input="handleUploadInput"
            ></textarea>
          </div>
          <el-button
            type="info"
            plain
            @click="upload(code, true)"
            style="margin-left: 190px; margin-top: 10px"
            >开始审计</el-button
          >
          <div id="uploadfile" style="margin-top: 10px; margin-left: 190px">
            <el-button type="primary" @click="file" plain>上传文件</el-button
            ><input
              type="file"
              id="file"
              @change="fileReader(getFileContent)"
              accept=".java"
            />
          </div>
        </span>
      </el-drawer>
      <!-- 返回首页按钮 -->
      <i
        class="el-icon-s-fold"
        size="20"
        @click="backmain"
        title="返回首页"
      ></i>
      <!-- 注销按钮 -->
      <i
        class="el-icon-switch-button"
        size="20"
        @click="logout"
        title="注销"
      ></i>
    </el-aside>
    <!-- 导航菜单 -->
    <el-aside class="nav" width="175px">
      <el-menu
        style="border: none"
        background-color="#202021"
        text-color="#dddddd"
        active-text-color="#2a5ba9"
      >
        <p class="menuHeader">
          <span><i class="el-icon-menu"></i>审计功能</span>
        </p>
        <!-- 审计菜单选项 -->
        <el-submenu index="0"
          ><template slot="title">Sql注入</template>
          <el-menu-item
            index="0"
            @click="aspectsShow(0)"
            style="padding-left: 20px; padding-right: 0px"
            >JDBC</el-menu-item
          >
          <el-menu-item
            index="1"
            @click="aspectsShow(1)"
            style="
              padding-left: 20px;
              padding-right: 0px;
              border-bottom: 1px solid #2d2d2e;
            "
            >MyBatis</el-menu-item
          >
        </el-submenu>
        <el-menu-item index="2" @click="aspectsShow(2)">文件上传</el-menu-item>
        <el-menu-item index="3" @click="aspectsShow(3)">路径穿越</el-menu-item>
        <el-menu-item index="4" @click="aspectsShow(4)">SSRF</el-menu-item>
        <el-menu-item index="5" @click="aspectsShow(5)">XEE</el-menu-item>
        <el-menu-item index="6" @click="aspectsShow(6)">RCE</el-menu-item>
      </el-menu>
    </el-aside>
    <!-- 代码审计主页面 -->
    <el-main>
      <!-- 审计方向显示 -->
      <span
        ><span style="color: #58595b; font-size: 13px">代码审计/</span
        >{{ auditaspects[dIndex] }}</span
      >
      <!-- 审计内容介绍 -->
      <el-row class="introduction">
        <el-col :span="3" style="text-align: center"
          >审计方向:7 &nbsp&nbsp&nbspupdating..</el-col
        >
        <el-col :span="4" style="text-align: center"
          >面向Java开发者的审计工具</el-col
        >
        <el-col :span="3" style="text-align: center">模块化审计</el-col>
        <el-col :span="3" style="text-align: center">Async实验室出品</el-col>
      </el-row>
      <el-row class="fun">
        <el-col :span="22"
          ><span><i class="el-icon-warning-outline"></i>使用说明</span></el-col
        >
        <el-col :span="22" class="reason" style="font-size: 15px"
          >&nbsp&nbsp&nbsp&nbsp{{ auditBug[dIndex].explain }}</el-col
        >
        <el-col :span="22"
          ><span><i class="el-icon-warning-outline"></i>漏洞原理</span></el-col
        >
        <el-col :span="22" class="reason"
          >&nbsp&nbsp&nbsp&nbsp{{ auditBug[dIndex].reason }}</el-col
        >
        <el-col
          ><span><i class="el-icon-message-solid"></i>解决方案</span></el-col
        >
        <el-col :span="22" class="method"
          >&nbsp&nbsp&nbsp&nbsp{{ auditBug[dIndex].solution }}</el-col
        >
        <el-col :span="22" class="learning"
          ><router-link tag="a" target="_blank" :to="{ name: learning[dIndex] }"
            ><el-button type="info" plain size="mini" style="float: right"
              >学习</el-button
            ></router-link
          ></el-col
        >
        <el-col :span="22"
          ><span><i class="el-icon-cpu"></i>代码比对</span>
        </el-col>
        <el-col :span="22" class="show">
          <div class="resultshow">
            <p style="font-size: 16px">&nbsp&nbsp&nbsp&nbsp结果展示</p>
            <!-- 审计结果显示 -->
            <!-- 状态说明模态框 -->
            <el-dialog
              title="状态显示说明"
              :visible.sync="dialogVisible"
              width="30%"
              @click="dialogVisible = false"
            >
              <span style="color: #000000; font-size: 16px">
                <p style="padding: 5px 0">
                  <i style="font-size: 16px; color: #9cb726">绿色：</i
                  >代码完全可行
                </p>
                <p style="padding: 5px 0">
                  <i style="font-size: 16px; color: #4169e1">蓝色：</i
                  >代码存在一定的风险
                </p>
                <p style="padding: 5px 0">
                  <i style="font-size: 16px; color: #ffd700">黄色：</i
                  >代码存在较大风险
                </p>
                <p style="padding: 5px 0">
                  <i style="font-size: 16px; color: #f0476d">红色：</i
                  >绝对不建议这样编写代码
                </p>
                <p style="padding: 5px 0">
                  <i style="font-size: 16px; color: #696969">灰色：</i>无法判断
                </p>
              </span>
            </el-dialog>
            <el-empty
              description="暂无数据"
              v-if="!isComplete[dIndex]"
            ></el-empty>
            <table v-else>
              <tr>
                <th>
                  状态&nbsp<i
                    class="el-icon-question"
                    style="font-size: 14px; cursor: pointer"
                    @click="dialogVisible = true"
                  ></i>
                </th>
                <th>审计结果</th>
              </tr>
              <tr>
                <td :style="{ color: auditColor[auditResult[dIndex].color] }">
                  {{ auditResult[dIndex].msg }}
                </td>
                <td>{{ auditResult[dIndex].data }}</td>
              </tr>
            </table>
          </div>
          <!-- 代码显示文本 -->
          <div class="codeshow">
            <p style="font-size: 16px">&nbsp&nbsp&nbsp&nbsp原始代码</p>
            <div class="colshow">
              <!-- 行数显示文本 -->
              <textarea
                id="rowshowaudit"
                rows="40"
                cols="2"
                wrap="off"
                disabled
              ></textarea>
            </div>
            <!-- 代码显示文本 -->
            <textarea
              id="auditcode"
              rows="40"
              cols="64"
              onscroll="document.getElemenById('rowshowaudit').scrollTop = this.scrollTop;"
              :value="this.filecode[dIndex]"
              disabled
            ></textarea>
          </div>
        </el-col>
      </el-row>
    </el-main>
    <div class="loader JS_on" v-show="isAuditing">
      <span class="binary"></span>
      <span class="binary"></span>
      <span class="getting-there">Auditinig</span>
    </div>
  </el-container>
</template>

<script>
import { mapState } from "vuex";
export default {
  name: "Audit",
  data() {
    return {
      dIndex: 0,
      auditResult: [],
      auditaspects: [
        "Sql注入/JDBC",
        "Sql注入/MyBatis",
        "文件上传",
        "路径穿越",
        "SSRF",
        "XEE",
        "RCE",
      ],
      learning: [
        "JDBC",
        "Mybatis",
        "Upload",
        "Traversial",
        "SSRF",
        "XEE",
        "RCE",
      ],
      auditColor: {
        red: "#f0476d",
        green: "#9cb726",
        blue: "#4169E1",
        yellow: "#FFD700",
        grey: "	#696969",
      },
      //审计结果集
      url: require("./media/default.png"),
      drawer: false,
      code: "",
      filecode: ["", "", "", "", "", "", ""],
      num: "",
      dialogVisible: false,
    };
  },
  methods: {
    goInfo() {
      this.$router.push("/info");
    },
    logout() {
      window.localStorage.setItem("isSet", false);
      this.$store.dispatch("logout");
      this.$router.push("/login");
    },
    backmain() {
      this.$router.push("/main");
    },
    aspectsShow(val) {
      this.dIndex = val;
    },
    async upload(code, flag) {
      if (flag) {
        if (confirm("确定输入完毕吗?")) {
          if (!code) this.$message.warning("输入不能为空");
          else {
            let file = new File([code], "test.txt", { type: "text/plain" });
            let form = new FormData();
            form.append("file", file);
            let data = new Object();
            data.file = form;
            data.index = this.dIndex;
            this.drawer = false;
            await this.$store.dispatch("audit", data);
            console.log(this.code);
            this.filecode[this.dIndex] = this.code;
            this.code = "";
            this.$message("审计完毕");
          }
        }
      } else {
        if (!code) this.$message.warning("输入不能为空");
        else {
          let file = new File([code], "test.txt", { type: "text/plain" });
          let form = new FormData();
          form.append("file", file);
          let data = new Object();
          data.file = form;
          data.index = this.dIndex;
          this.drawer = false;
          await this.$store.dispatch("audit", data);
          this.$message.success("审计完毕");
        }
      }
    },
    //绑定上传文件按钮与input上传文件
    file() {
      var target = document.querySelector("#file");
      file.click();
    },
    //上传文件
    fileReader(callback) {
      let tar = document.querySelector("#file");
      let file = tar.files[0];
      if (file) {
        let reader = new FileReader();
        reader.readAsText(file, "UTF-8");
        reader.onload = function (e) {
          let content = e.target.result;
          if (callback && typeof callback == "function") {
            callback(content);
          }
        };
      }
    },
    getFileContent(content) {
      console.log(content);
      this.filecode[this.dIndex] = content;
      this.upload(content, false);
    },
    //文本域保留tab键
    insertInputTxt(id, insertTxt) {
      var elInput = document.getElementById(id);
      var startPos = elInput.selectionStart;
      var endPos = elInput.selectionEnd;
      if (startPos === undefined || endPos === undefined) return;
      var txt = elInput.value;
      var result =
        txt.substring(0, startPos) + insertTxt + txt.substring(endPos);
      elInput.value = result;
      elInput.focus();
      elInput.selectionStart = startPos + insertTxt.length;
      elInput.selectionEnd = startPos + insertTxt.length;
    },
    tabFunc(e) {
      if (e.keyCode == 9) {
        e.preventDefault();
        this.insertInputTxt("uploadcode", "\t");
      }
    },
    //显示代码行数(审计结果界面)
    handleAuditInput() {
      var target = document.getElementById("auditcode");
      var str = target.value;
      str = str.replace(/\r/gi, "");
      str = str.split("\n");
      var n = str.length;
      var lineobj = document.getElementById("rowshowaudit");
      for (var j = 1; j <= n; j++) {
        if (document.all) {
          this.num += j + "\r\n"; //判断浏览器是否是IE
        } else {
          this.num += j + "\n";
        }
      }
      lineobj.value = this.num;
      this.num = "";
    },
    //显示代码行数(文件上传界面)
    handleUploadInput() {
      var target = document.getElementById("uploadcode");
      var str = target.value;
      str = str.replace(/\r/gi, "");
      str = str.split("\n");
      var n = str.length;
      var lineobj = document.getElementById("rowshowupload");
      for (var j = 1; j <= n; j++) {
        if (document.all) {
          this.num += j + "\r\n"; //判断浏览器是否是IE
        } else {
          this.num += j + "\n";
        }
      }
      lineobj.value = this.num;
      this.num = "";
    },
  },
  computed: {
    ...mapState({
      //代码审计漏洞原理、解决方案数据
      auditBug: (state) => state.audit.auditBug,
      jdbc: (state) => state.audit.jdbc,
      mybatis: (state) => state.audit.mybatis,
      UPLOAD: (state) => state.audit.upload,
      traversal: (state) => state.audit.traversal,
      SSRF: (state) => state.audit.SSRF,
      XEE: (state) => state.audit.XEE,
      RCE: (state) => state.audit.RCE,
      isComplete: (state) => state.audit.isComplete,
      isAuditing: (state) => state.audit.isAuditing,
    }),
  },
  //监视漏洞返回集，当有变化时，及时更改data中的数据
  watch: {
    jdbc: {
      deep: true,
      handler() {
        this.auditResult[0] = this.jdbc;
      },
    },
    mybatis: {
      deep: true,
      handler() {
        this.auditResult[1] = this.mybatis;
      },
    },
    UPLOAD: {
      deep: true,
      handler() {
        this.auditResult[2] = this.UPLOAD;
      },
    },
    traversal: {
      deep: true,
      handler() {
        this.auditResult[3] = this.traversal;
      },
    },
    SSRF: {
      deep: true,
      handler() {
        this.auditResult[4] = this.SSRF;
      },
    },
    XEE: {
      deep: true,
      handler() {
        this.auditResult[5] = this.XEE;
      },
    },
    RCE: {
      deep: true,
      handler() {
        this.auditResult[6] = this.RCE;
      },
    },
    dIndex: {
      handler() {
        var code = document.getElementById("auditcode");
        code.value = this.filecode[this.dIndex];
      },
    },
  },
  //初始化aduitResult
  created() {
    this.auditResult = [
      this.jdbc,
      this.mybatis,
      this.UPLOAD,
      this.traversal,
      this.SSRF,
      this.XEE,
      this.RCE,
    ];
  },
  mounted() {
    //获取auditBug相关的数据
    this.$store.dispatch("auditBug");
    if (window.localStorage.getItem("isLog")) console.log(1);
  },
};
</script>

<style scoped lang="less">
.elcontainer {
  position: relative;
}

.el-container .loader {
  position: absolute;
  width: 300px;
  height: 300px;
  left: 50%;
  margin-left: -150px;
  top: 50%;
  margin-top: -150px;
  z-index: 999;
  background: #2d4654;
}
//加载动画
.loader {
  position: relative;

  &::before,
  &::after {
    content: "";
    width: 0;
    height: 0;
    position: absolute;
    bottom: 30px;
    left: 100px;
    top: 130px;
    z-index: 1;
    border-left: 50px solid transparent;
    border-right: 50px solid transparent;
    border-bottom: 20px solid darken(#2d4654, 10%);
    transform: scale(0);
    transition: all 0.2s ease;
  }
  &::after {
    border-right: 15px solid transparent;
    border-bottom: 20px solid darken(#2d4654, 13%);
  }

  .getting-there {
    width: 120%;
    text-align: center;
    position: absolute;
    top: 150px;
    left: -8%;
    font-family: "Lato";
    font-size: 12px;
    letter-spacing: 2px;
    color: white;
  }

  .binary {
    width: 100%;
    height: 140px;
    display: block;
    color: white;
    position: absolute;
    top: 0;
    left: 100px;
    z-index: 2;
    overflow: hidden;

    &::before,
    &::after {
      font-family: "Lato";
      font-size: 24px;
      position: absolute;
      top: 0;
      left: 0;
      opacity: 0;
    }

    &:nth-child(1) {
      &::before {
        content: "0";
        animation: a 1.1s linear infinite;
      }
      &::after {
        content: "0";
        animation: b 1.3s linear infinite;
      }
    }
    &:nth-child(2) {
      &::before {
        content: "1";
        animation: c 0.9s linear infinite;
      }
      &::after {
        content: "1";
        animation: d 0.7s linear infinite;
      }
    }
  }

  // ACTIVATE
  &.JS_on {
    &::before,
    &::after {
      transform: scale(1);
    }
  }
}

@keyframes a {
  0% {
    transform: translate(30px, 0) rotate(30deg);
    opacity: 0;
  }
  100% {
    transform: translate(30px, 150px) rotate(-50deg);
    opacity: 1;
  }
}
@keyframes b {
  0% {
    transform: translate(50px, 0) rotate(-40deg);
    opacity: 0;
  }
  100% {
    transform: translate(40px, 150px) rotate(80deg);
    opacity: 1;
  }
}
@keyframes c {
  0% {
    transform: translate(70px, 0) rotate(10deg);
    opacity: 0;
  }
  100% {
    transform: translate(60px, 150px) rotate(70deg);
    opacity: 1;
  }
}
@keyframes d {
  0% {
    transform: translate(30px, 0) rotate(-50deg);
    opacity: 0;
  }
  100% {
    transform: translate(45px, 150px) rotate(30deg);
    opacity: 1;
  }
}

.el-aside,
.el-main {
  background-color: #202021;
}
