}

.resizeNone {
  .el-textarea__inner {
    resize: none;
  }
}

.resultshow table {
  width: 100%;
}

.resultshow table tr {
  border-bottom: 1px solid #2d2d2e;
}

.resultshow table th,
.resultshow table td {
  width: 50%;
  line-height: 24px;
  color: #dddddd;
  height: 24px;
}

