import { reqgetHistory } from '../../api/index'
const state = {
    history: [],
};
const mutations = {
    HISTORY(state, data) {
        state.history = data;
    }
};
const actions = {
    async getHistory({ commit }) {
        let result = await reqgetHistory()
        if (result.code == 1) {
            commit("HISTORY", result.data);
        } else {
            this.$message.error(result.msg);
        }
    }
};
const getters = {};

export default {
    state,
    mutations,
    actions,
    getters,
}