    <!-- 文章内容 -->
    <el-main class="main">
      <h1
        style="
          font-size: 35px;
          font-weight: bold;
          text-align: center;
          background-color: rgb(191, 198, 192);
          color: rgb(73, 71, 70);
          line-height: 80px;
          border-left: 10px solid rgb(74, 147, 74);
        "
      >
        文件上传
      </h1>
      <div class="passage1">
        <h1 id="concepts">相关概念</h1>
        <br />
        <h3 id="concepts1" class="contenthead">概述</h3>
        <el-row>
          <el-col :span="24" class="concepts1" style="line-height: 25px">
            &nbsp&nbsp&nbsp&nbsp我们经常会将一些图片、文档、压缩包之类的文件上传到远程服务器上进行保存。文件上传漏洞攻击指攻击者利用系统缺陷绕过对文件的验证和处理，将恶意文件（如配置文件、木马和病毒、包含脚本的图片等）上传到服务器并通过文件获得服务器上相应的权力，或通过诱导外部用户访问、下载上传的病毒或木马文件，达到攻击的目的。<br />
            文件上传漏洞类型如图所示：<br />
            <img src="./media/basic.jpg" alt="" style="width: 100%" />
          </el-col>
        </el-row>
        <br />
        <h3 id="concepts2" class="contenthead">文件漏洞产生原因</h3>
        <el-row class="bigger">
          <el-col :span="24">
            未对文件做任何过滤，可上传任意文件类型，如木马、可执行文件等；<br />
            仅在js端检验文件后缀，可通过删除js或禁用js或抓包修改文件后缀等方法绕过；<br />
            后端后缀过滤使用黑名单，过滤不全，可通过使用未过滤的后缀名、大小写变换、双写后缀名、双后缀名、文件名结尾加”.“或空格、%00截断等方式绕过；<br />
            后端判断文件类型，只判断Content-type，可通过抓包修改Content-type字段的值进行绕过；<br />
            后端检查文件内容仅检查文件头内容，可通过抓包添加合法文件头，或使用其他工具添加合法文件头进行绕过。<br />
          </el-col>
        </el-row>
        <h3 id="concepts3" class="contenthead">文件漏洞类型</h3>
        <br />
        <h5>1 无任何过滤</h5>
        <el-row class="bigger">
          <el-col :span="24">
            &nbsp&nbsp&nbsp&nbsp服务端脚本语言未对上传的文件进行任何限制和过滤，导致恶意用户上传任意文件。<br />
            <div class="try">
              <div class="tryhead">
                <span class="mark"
                  ><div style="background-color: rgb(252, 98, 93)"></div>
                  <div style="background-color: rgb(253, 188, 64)"></div>
                  <div style="background-color: rgb(53, 205, 75)"></div
                ></span>
                <span style="color: rgb(144, 164, 174)" class="language"
                  >JAVA && HTML</span
                >
              </div>
              <div class="code">
                <div class="colshow">
                  <textarea
                    id="colshow"
                    class="rowshow"
                    warp="off"
                    rows="15"
                    disabled
                  ></textarea>
                </div>
                <textarea
                  id="code"
                  class="codeshow"
                  rows="15"
                  disabled
                  onscroll="document.getElementsByClassName('rowshow')[0].scrollTop = this.scrollTop;"
                  v-model="code1"
                ></textarea>
              </div>
            </div>
            上述代码未对上传的文件进行任何检测，可以上传任意类型的文件，包括exe文件和木马等。
          </el-col>
        </el-row>
        <br /><br />
        <h5>2 客户端检测绕过</h5>
        <el-row class="bigger">
          <el-col :span="24">
            &nbsp&nbsp&nbsp&nbsp通过前端js检测文件名是否合法没有任何意义，因为任意用户都可以对前端js进行任意修改或者通过burp
            suite抓包修改上传的文件名。<br />
            <div class="try">
              <div class="tryhead">
                <span class="mark"
                  ><div style="background-color: rgb(252, 98, 93)"></div>
                  <div style="background-color: rgb(253, 188, 64)"></div>
                  <div style="background-color: rgb(53, 205, 75)"></div
                ></span>
                <span style="color: rgb(144, 164, 174)" class="language"
                  >HTML && Javascript</span
                >
              </div>
              <div class="code">
                <div class="colshow">
                  <textarea
                    id="colshow"
                    class="rowshow"
                    warp="off"
                    rows="15"
                    disabled
                  ></textarea>
                </div>
                <textarea
                  id="code"
                  class="codeshow"
                  rows="15"
                  disabled
                  onscroll="document.getElementsByClassName('rowshow')[1].scrollTop = this.scrollTop;"
                  v-model="code2"
                ></textarea>
              </div>
            </div>
            &nbsp&nbsp&nbsp&nbsp后端没有进行任何限制，将文件存储在src\main\resources\static\upload下
            <div class="try">
              <div class="tryhead">
                <span class="mark"
                  ><div style="background-color: rgb(252, 98, 93)"></div>
                  <div style="background-color: rgb(253, 188, 64)"></div>
                  <div style="background-color: rgb(53, 205, 75)"></div
                ></span>
                <span style="color: rgb(144, 164, 174)" class="language"
                  >JAVA</span
                >
              </div>
              <div class="code">
                <div class="colshow">
                  <textarea
                    id="colshow"
                    class="rowshow"
                    warp="off"
                    rows="15"
                    disabled
                  ></textarea>
                </div>
                <textarea
                  id="code"
                  class="codeshow"
                  rows="15"
                  disabled
                  onscroll="document.getElementsByClassName('rowshow')[2].scrollTop = this.scrollTop;"
                  v-model="code3"
                ></textarea>
              </div>
            </div>
          </el-col>
        </el-row>
        <br /><br />
        <h5>3 文件头检测绕过</h5>
        <el-row class="bigger">
          <el-col :span="24">
            &nbsp&nbsp&nbsp&nbsp根据文件的前面几个字节，即常说的魔术数字进行判断，不同文件类型的开头几个字节不同。<br />
            <div class="try">
              <div class="tryhead">
                <span class="mark"
                  ><div style="background-color: rgb(252, 98, 93)"></div>
                  <div style="background-color: rgb(253, 188, 64)"></div>
                  <div style="background-color: rgb(53, 205, 75)"></div
                ></span>
                <span style="color: rgb(144, 164, 174)" class="language"
                  >JAVA</span
                >
              </div>
              <div class="code">
                <div class="colshow">
                  <textarea
                    id="colshow"
                    class="rowshow"
                    warp="off"
                    rows="15"
                    disabled
                  ></textarea>
                </div>
                <textarea
                  id="code"
                  class="codeshow"
                  rows="15"
                  disabled
                  onscroll="document.getElementsByClassName('rowshow')[3].scrollTop = this.scrollTop;"
                  v-model="code4"
                ></textarea>
              </div>
            </div>
          </el-col>
        </el-row>
        <br /><br />
        <h5>4 ImageIO判断上传图片文件</h5>
        <el-row class="bigger">
          <el-col :span="24">
            &nbsp&nbsp&nbsp&nbsp通过过ImageReader解码file并返回一个BufferedImage对象，如果找不到合适的ImageReader则会返回null，我们可以认为这不是图片文件。<br />
            &nbsp&nbsp&nbsp&nbsp如果能够正常的获取到一张图片的宽高属性，那么该文件一定是图片，因为非图片文件获取不到它的宽高属性的。<br />
            &nbsp&nbsp&nbsp&nbsp但若是在可以正常打开的图片里面加入非法代码或者病毒，那就非常危险了。<br />
            <div class="try">
              <div class="tryhead">
                <span class="mark"
                  ><div style="background-color: rgb(252, 98, 93)"></div>
                  <div style="background-color: rgb(253, 188, 64)"></div>
                  <div style="background-color: rgb(53, 205, 75)"></div
                ></span>
                <span style="color: rgb(144, 164, 174)" class="language"
                  >JAVA</span
                >
              </div>
              <div class="code">
                <div class="colshow">
                  <textarea
                    id="colshow"
                    class="rowshow"
                    warp="off"
                    rows="15"
                    disabled
                  ></textarea>
                </div>
                <textarea
                  id="code"
                  class="codeshow"
                  rows="15"
                  disabled
                  onscroll="document.getElementsByClassName('rowshow')[4].scrollTop = this.scrollTop;"
                  v-model="code5"
                ></textarea>
              </div>
            </div>
          </el-col>
        </el-row>
      </div>
      <div class="passage2">
        <h1 id="methods">编码建议</h1>
        <br />
        <el-row style="line-height: 25px">
          <el-col>
            服务器端的检查最好使用白名单过滤的方法，黑名单极不可靠；<br />
            使用随机数改写文件名和文件路径。文件上传如果要执行代码，需要用户能够访问到这个文件。应用了随机数改写了文件名和路径，可防止大小写绕过、双后缀、多后缀等手段，将极大地增加攻击的成本；<br />
            文件上传目录设置为不可执行，只要web容器无法解析该目录下面的文件，即使攻击者上传了脚本文件，服务器本身也不会受到影响。<br />
            使用安全设备防御，恶意文件千变万化，隐藏手法也不断推陈出新，对普通的系统管理员来说可以通过部署安全设备来帮助防御。<br />
          </el-col>
        </el-row>
      </div>
    </el-main>
    <div class="menu"></div>
  </el-container>
</template>

