import { reqGetveri, reqGetverirsl, reqPostLogup, reqPostLogin } from '../../api/index'
const state = {
    isLog: false,
    loguprsl: {},
    loginrsl: {},
};
const mutations = {
    LOGUP(state, data) {
        state.loguprsl = data;
    },
    LOGIN(state, data) {
        state.loginrsl = data;
    },
    ISLOG(state, data) {
        state.isLog = data;
    }
};
const actions = {
    //用户注册
    async logup({ commit }, userinfo) {
        let result = await reqPostLogup(userinfo);
        commit("LOGUP", result);
    },
    //用户登录
    async login({ commit }, userinfo) {
        let result = await reqPostLogin(JSON.stringify(userinfo));
        await commit("LOGIN", result);
        commit("ISLOG", true);
    },
};
const getters = {};

export default {
    state,
    mutations,
    actions,
    getters,
}