import { reqGetDanger1, reqGetSafe, reqGetDanger2 } from '../../api/index'
const state = {
    danger1: "",
    danger2: "返回结果为空",
    safe: "返回结果为空",
};
const mutations = {
    DANGEER1(state, data) {
        state.danger1 = data;
    },
};
const actions = {
    async getResult({ commit }) {
        let result = await reqGetDanger1();
        if (result.code == 1) {
            commit("DANGEER1", result.data);
        }
    }
};
const getters = {};

export default {
    state,
    mutations,
    actions,
    getters,
}