<template>
  <div class="container">
    <!-- 导航栏 -->
    <div class="nav">
      <div>
        <img class="logo1" src="./media/logo.png" />
      </div>
      <div>
        <img class="logo2" src="./media/ASYNC(white).png" />
      </div>
      <div class="userinfo" v-show="isLog">
        <div class="img">
          <img src="./media/default.png" style="width: 100%; height: 70%" />
        </div>
        <span style="font-size: 15px; color: #ffffff; text-align: center">{{
          username
        }}</span>
      </div>
    </div>
    <!-- 介绍 -->
    <div class="scriptshow">
      <p class="static">Cakepainter</p>
      <p class="static">面向java开发者的</p>
      <p class="static">一站式审计工具</p>
      <transition appear name="change" @after-enter="rollshow">
        <p class="dynamic" v-if="change">
          {{ dynamicscript[index] }}
        </p>
      </transition>
      <button
        id="rollshow"
        @click="
          change = !change;
          if (!change) index++;
          if (index == 7) index = 0;
        "
      >
        点击
      </button>
    </div>
    <!-- 开始审计之旅 -->
    <div class="golog">
      <button v-if="!isLog" @click="golog">登录</button>
      <button v-else @click="goAudit">开始审计之旅</button>
    </div>
    <!-- 功能展示 -->
    <div class="funshow">
      <div class="explain">
        <div class="done"></div>
        <span>已完成内容</span>
        <div class="doing"></div>
        <span>正在发开中</span>
      </div>
      <router-link tag="a" target="_blank" to="/learning/jdbc">
        <div class="fun">
          <p class="description">SQL注入/JDBC</p>
          <img src="./media/box.png" alt="" />
        </div>
      </router-link>
      <router-link tag="a" target="_blank" to="/learning/mybatis">
        <div class="fun">
          <p class="description">SQL注入/Mybatis</p>
          <img src="./media/store.png" alt="" />
        </div>
      </router-link>
      <router-link tag="a" target="_blank" to="/learning/upload">
        <div class="fun">
          <p class="description">上传文件漏洞</p>
          <img src="./media/upload.png" alt="" />
        </div>
      </router-link>
      <router-link tag="a" target="_blank" to="/learning/traversial">
        <div class="fun">
          <p class="description">路径穿越漏洞</p>
          <img src="./media/traversal.png" alt="" />
        </div>
      </router-link>
      <router-link tag="a" target="_blank" to="/learning/ssrf">
        <div class="fun">
          <p class="description">SSRF漏洞</p>
          <img src="./media/wall.png" alt="" />
        </div>
      </router-link>
      <router-link tag="a" target="_blank" to="/learning/xee">
        <div class="fun">
          <p class="description">XEE漏洞</p>
          <img src="./media/attack.png" alt="" />
        </div>
      </router-link>
      <router-link tag="a" target="_blank" to="/learning/rce">
        <div class="fun">
          <p class="description">RCE漏洞</p>
          <img src="./media/remote.png" alt="" />
        </div>
      </router-link>
      <router-link tag="a" target="_blank" to="/learning/xss">
        <div class="fun yet">
          <p class="description">XSS漏洞</p>
          <img src="./media/doing.png" alt="" />
        </div>
      </router-link>
      <router-link tag="a" target="_blank" to="/learning/reverse">
        <div class="fun yet">
          <p class="description">反序列化</p>
          <img src="./media/doing.png" alt="" />
        </div>
      </router-link>
    </div>
    <div class="log"></div>
  </div>
</template>

<script>
import { mapState } from "vuex";
let rollshow;
export default {
  name: "Main",
  data() {
    return {
      change: true,
      isSet: false,
      index: 0,
      dynamicscript: [
        "SQL注入",
        "文件上传漏洞",
        "路径穿越漏洞",
        "SSRF漏洞",
        "XEE漏洞",
        "RCE漏洞",
      ],
      learning: [
        "JDBC",
        "Mybatis",
        "Upload",
        "Traversial",
        "SSRF",
        "XEE",
        "RCE",
      ],
      username: "",
      log: "开始审计之旅",
      notLog: "登录",
    };
  },
  methods: {
    rollshow() {
      if (!this.isSet) {
        this.Set = true;
        setInterval(() => {
          rollshow.click();
          setTimeout(() => {
            rollshow.click();
          }, 200);
        }, 4200);
      }
    },
    golog() {
      this.$router.push("/login");
    },
    goAudit() {
      this.$router.push("/audit");
    },
  },
  created() {
    this.username = window.localStorage.getItem("username");
  },
  computed: {
    ...mapState({
      isLog: (state) => state.login.isLog,
    }),
  },
  mounted() {
    rollshow = document.getElementById("rollshow");
  },
};
</script>

<style scoped lang="css">
/* 动画部分 */
.container {
  position: relative;
  width: 100%;
  height: 100%;
  background-color: #020516;
}

.nav {
  position: relative;
  width: 100%;
  height: 15%;
  background-color: rgb(39, 40, 44);
}

.nav div {
  width: 10%;
  height: 100%;
  float: left;
}

.nav .logo1 {
  width: 100%;
  height: 100%;
}

.nav .logo2 {
  margin-top: -30px;
  margin-left: 10px;
  width: 100%;
  height: 160%;
}

.nav .userinfo {
  margin-left: 1000px;
  position: relative;
}

.nav .userinfo .img {
  position: absolute;
  top: 50%;
  margin-top: -38px;
  width: 50%;
  height: 100%;
}

.nav .userinfo span {
  position: absolute;
  top: 50%;
  margin-top: -11px;
  left: 57%;
}

#rollshow {
  display: none;
}

.scriptshow {
  position: absolute;
  width: 40%;
  height: 40%;
  top: 30%;
  left: 3%;
  font-size: 50px;
  font-weight: 800;
}

.scriptshow .static {
  color: #fffeff;
}

.scriptshow .dynamic {
  color: #ebba2f;
}
/* 滚动功能动画部分 */
.change-enter {
  opacity: 0;
  transform: translateY(24%);
}

.change-enter-active {
  transition: all 4s;
}

.change-enter-to {
  opacity: 1;
}
/* 滚动功能动画部分 */
.golog {
  position: absolute;
  width: 150px;
  height: 60px;
  line-height: 60px;
  top: 67%;
  left: 3%;
}

.golog button {
  width: 150px;
  height: 60px;
  border-radius: 20px;
  font-size: 20px;
  font-weight: 600;
  background-color: #ffffff;
  color: #38383a;
  border: none;
}

.golog button:hover {
  cursor: pointer;
  background-color: rgb(153, 167, 217);
  border: none;
}

.funshow {
  position: absolute;
  width: 50%;
  height: 52%;
  right: 3%;
  top: 18%;
}

.funshow .explain {
  line-height: 10px;
  height: 10px;
  margin-bottom: 20px;
}

.funshow .explain div {
  display: inline-block;
  width: 20px;
  height: 10px;
  border-radius: 10px;
  margin-right: 7px;
}

.funshow .explain .done {
  background-color: #9ee048;
}

.funshow .explain span {
  color: #fffeff;
  font-weight: 500;
}

.funshow .explain .doing {
  margin-left: 10px;
  background-color: #0c33b6;
}

.funshow .fun {
  float: left;
  margin-right: 15px;
  margin-top: 13px;
  width: 23%;
  height: 45%;
  background-color: #9ee048;
  transition: all 0.5s;
  overflow: hidden;
}

.funshow .fun:hover {
  cursor: pointer;
  transform: translateY(-3%);
}

.funshow .fun img {
  width: 100%;
  height: 100%;
}

.funshow .yet {
  background-color: #0c33b6;
}

.funshow .fun .description {
  color: #071109;
  font-size: 20px;
  text-align: center;
  font-weight: 600;
}

.funshow .yet .description {
  color: #fffeff;
}
</style>