      <div class="contents">
        <h3>
          <a href="javascript:;" @click="jump('#concepts')">一.相关概念</a>
        </h3>
        <h5>
          <a
            href="javascript:;"
            @click="jump('#concepts1')"
            style="margin-left: 13px; font-size: 13px"
            >1.概述</a
          >
        </h5>
        <h5>
          <a
            href="javascript:;"
            @click="jump('#concepts2')"
            style="margin-left: 13px; font-size: 13px"
            >2.前提</a
          >
        </h5>
        <h3>
          <a href="javascript:;" @click="jump('#methods')">二.危害分析</a>
        </h3>
        <h3>
          <a href="javascript:;" @click="jump('#try')">三.漏洞分析</a>
        </h3>
      </div>
    </div>
    <!-- 文章内容 -->
    <el-main class="main">
      <h1
        style="
          font-size: 35px;
          font-weight: bold;
          text-align: center;
          background-color: rgb(191, 198, 192);
          color: rgb(73, 71, 70);
          line-height: 80px;
          border-left: 10px solid rgb(74, 147, 74);
        "
      >
        RCE - 远程命令执行漏洞
      </h1>
      <div class="passage1">
        <h1 id="concepts">相关概念</h1>
        <br />
        <h3 id="concepts1" class="contenthead">概述</h3>
        <el-row>
          <el-col :span="24" class="concepts1">
            &nbsp&nbsp&nbsp&nbspRCE (Remote Code Execution),
            既远程代码执行漏洞，
            可以让攻击者直接向后台服务器远程注入操作系统命令或者代码，从而控制后台系统。
            包含两种类型：
            <br />&nbsp&nbsp命令注入（Command
            Injection），在某种开发需求中，需要引入对系统本地命令的支持来完成特定功能，当未对输入做过滤时，则会产生命令注入。
            <br />
            &nbsp&nbsp代码注入（Code
            Injection），在正常的java程序中注入一段java代码并执行，即用户输入的数据当作java代码进行执行。
          </el-col>
        </el-row>
        <br />
        <h3 id="concepts2" class="contenthead">前提</h3>
        <el-row>
          <el-col :span="24">
            &nbsp&nbsp&nbsp&nbsp命令执行漏洞产生于需要调用一些执行系统命令的函数，既当我们使用这些能与系统命令产生联系的函数时，又没有对用户使用这个方法进行过滤和限制就会产生这个漏洞。
            在java开发语言中可以执行系统命令的函数有Runtime.getRuntime.exec和ProcessBuilder.start,其中,Runtime.getRuntime.exec是在java1.5之前提供的，java1.5之后提供了Process
            Builder来构建进程。
          </el-col>
        </el-row>
      </div>
      <div class="passage2">
        <h1 id="methods">危害分析</h1>
        <br />
        <el-row>
          <el-col>
            &nbsp&nbsp&nbsp&nbsp主要有以下几个危害：继承Web服务程序的权限
            去执行系统命令或读/写文件。反弹shell，控制整个网站甚至控制服务器，进一步实现内网渗透。
          </el-col>
        </el-row>
      </div>
      <div class="passage3">
        <h1 id="try">漏洞分析</h1>
        <h3 class="contenthead">
          <i class="el-icon-warning-outline"></i>ProcessBuilder命令执行漏洞
        </h3>
        <el-row class="bigger">
          <el-col :span="24">
            &nbsp&nbsp&nbsp&nbspjava.lang.ProceesBuilder类用于创建操作系统进程，每个ProcessBuilder实列管理一个进程属性集。start（）方法利用这些属性创建一个新的Process实列，
            可以利用ProcessBuilder执行命令。<br />
            ProcessBuilder执行命令的方式如下：<br />
            <i style="font-size: 20px; font-weight: 600"
              >&nbsp&nbsp&nbsp&nbsp&nbsp&nbsp&nbsp&nbspProcessBuilder pb=new
              ProcessBuilder("command")<br />
              &nbsp&nbsp&nbsp&nbsp&nbsp&nbsp&nbsp&nbspProcess
              process=pb.start(); </i
            ><br />
            &nbsp&nbsp&nbsp&nbsp而这种方式，执行的命令是未经过滤的，用户可以控制我们的"command",shell程序执行命令含有;的分隔符
            当用户传入参数含有;时，我们并没有进行过滤，既用户可以传入一条他自己想要的指令，来对我们的程序进行攻击
            <div class="try">
              <div class="tryhead">
                <span class="mark"
                  ><div style="background-color: rgb(252, 98, 93)"></div>
                  <div style="background-color: rgb(253, 188, 64)"></div>
                  <div style="background-color: rgb(53, 205, 75)"></div
                ></span>
                <span style="color: rgb(144, 164, 174)" class="language"
                  >java</span
                >
              </div>
              <div class="code">
                <div class="colshow">
                  <textarea
                    id="colshow"
                    class="rowshow"
                    warp="off"
                    rows="15"
                    disabled
                  ></textarea>
                </div>
                <textarea
                  id="code"
                  class="codeshow"
                  rows="15"
                  disabled
                  onscroll="document.getElementsByClassName('rowshow')[0].scrollTop = this.scrollTop;"
                  v-model="code1"
                ></textarea>
              </div>
            </div>
          </el-col>
        </el-row>
        <br />
        <el-row class="bigger">
          <h3 class="contenthead">
            <i class="el-icon-warning-outline"></i>Runtime exec命令执行漏洞
          </h3>
          <el-col :span="24">
            &nbsp&nbsp&nbsp&nbspjava.lang.Runtime公共类中的exec()方法同样也可以执行系统命令，exec()方法的使用方式有以下6种：
            <div class="try">
              <div class="tryhead">
                <span class="mark"
                  ><div style="background-color: rgb(252, 98, 93)"></div>
                  <div style="background-color: rgb(253, 188, 64)"></div>
                  <div style="background-color: rgb(53, 205, 75)"></div
                ></span>
                <span style="color: rgb(144, 164, 174)" class="language"
                  >java</span
                >
              </div>
              <div class="code">
                <div class="colshow">
                  <textarea
                    id="colshow"
                    class="rowshow"
                    warp="off"
                    rows="15"
                    disabled
                  ></textarea>
                </div>
                <textarea
                  id="code"
                  class="codeshow"
                  rows="15"
                  disabled
                  onscroll="document.getElementsByClassName('rowshow')[1].scrollTop = this.scrollTop;"
                  v-model="code3"
                ></textarea>
              </div>
            </div>
            exec()方法在执行命令时，当传入的参数为字符串参数和数组参数是时会有不同的返回结果。
            而当使用数组参数时，就会有远程注入的风险，因此当使用该方法时，建议使用第一种传参方式
            <div class="try">
              <div class="tryhead">
                <span class="mark"
                  ><div style="background-color: rgb(252, 98, 93)"></div>
                  <div style="background-color: rgb(253, 188, 64)"></div>
                  <div style="background-color: rgb(53, 205, 75)"></div
                ></span>
                <span style="color: rgb(144, 164, 174)" class="language"
                  >java</span
                >
              </div>
              <div class="code">
                <div class="colshow">
                  <textarea
                    id="colshow"
                    class="rowshow"
                    warp="off"
                    rows="15"
                    disabled
                  ></textarea>
                </div>
                <textarea
                  id="code"
                  class="codeshow"
                  rows="15"
                  disabled
                  onscroll="document.getElementsByClassName('rowshow')[2].scrollTop = this.scrollTop;"
                  v-model="code2"
                ></textarea>
              </div>
            </div>
          </el-col>
        </el-row>
      </div>
    </el-main>
