<script>
export default {
  name: "Learning",
  data() {
    return {
      num: "",
      code1: ` /**
     * @vul 调用ProcessBuilder执行ls命令，接收参数filepath，拼接命令语句
     * @poc http:///rce/study/processBuilder?filepath=/tmp;whoami
     */
    @GetMapping("study/processBuilder")
    public static String cmd(String filepath) {
        // 提供一个命令字典
        String[] cmdList = {"sh", "-c", "ls -l " + filepath};
        StringBuilder sb = new StringBuilder();
        String line;

        // 利用指定的操作系统程序和参数构造一个进程生成器
        ProcessBuilder pb = new ProcessBuilder(cmdList);
        pb.redirectErrorStream(true);

        // 使用此进程生成器的属性启动一个新进程
        Process process = null;
        try {
            System.out.println("[Vul] 执行ProcessBuilder：" + filepath);
            process = pb.start();
            // 取得命令结果的输出流
            InputStream fis = process.getInputStream();
            // 用一个读输出流类去读
            InputStreamReader isr = new InputStreamReader(fis);
            // 用缓存器读行
            BufferedReader br = new BufferedReader(isr);
            //直到读完为止
            while ((line = br.readLine()) != null) {
                sb.append(line);
            }
        } catch (IOException e) {
            e.printStackTrace();
        }
        return sb.toString();
    }`,
      code2: ` 修复了的:
    @RequestMapping("study/processBuilder/safe")
    public static String processBuilderSafe(String filepath) {

        if (! Security.checkOs(filepath)) {

            String[] cmdList = {"sh", "-c", "ls -l " + filepath};
            StringBuilder sb = new StringBuilder();
            String line;

            ProcessBuilder pb = new ProcessBuilder(cmdList);
            pb.redirectErrorStream(true);

            Process process = null;
            try {
                process = pb.start();
                InputStream fis = process.getInputStream();
                InputStreamReader isr = new InputStreamReader(fis);
                BufferedReader br = new BufferedReader(isr);

                while ((line = br.readLine()) != null) {
                    System.out.println(line);
                    sb.append(line);
                }
            } catch (IOException e) {
                e.printStackTrace();
            }
            return sb.toString();
        } else {
            return "检测到非法命令注入";
        }
    }

    runtime：
    @RequestMapping("study/runtime2")
    public static void cmd3(String cmd) {
        String test = ";echo 1 > 1.txt";
        String Command = "ping 127.0.0.1" + test;

        try {
            Runtime.getRuntime().exec(Command);
        } catch (IOException e) {
            e.printStackTrace();
        }
        //Runtime.getRuntime().exec(new String[]{"/bin/sh", "-c", Command}); //打了//的是要产生漏洞的
    }`,
      code3: `//在单独的进程种执行指定的字符串命令
                  public Process exec(String command)
                  //在单独的进程中执行指定的命令和参数
                  public Process exec(String[] cmdarray)
                  //在具有指定环境的单独进程种执行指定的命令和参数
                  public Process exec(String[] cmdarray,String[] envp)
                  //在具有指定环境和工作目录的单独进程种执行指令的命令和参数
                  public Process exec(String[] cmdarray,String[] envp,File
                  dir)
                  //在具有指定环境的单独进程种执行指定的字符串命令
                  public Process exec(String command,String[] envp)
                  //在具有指定环境和工作目录的单独进程种执行指定的字符串命令
                  public Process exec(String command,String[] envp ,File dir)`,
    };
  },
  methods: {
    //textarea自动显示行数
    handleTextareaInput() {
      var target = document.getElementsByClassName("codeshow");
      for (let i = 0; i < target.length; i++) {
        var str = target[i].value;
        str = str.replace(/\r/gi, "");
        str = str.split("\n");
        var n = str.length;
        var lineobj = document.getElementsByClassName("rowshow")[i];
        for (var j = 1; j <= n; j++) {
          if (document.all) {
            this.num += j + "\r\n"; //判断浏览器是否是IE
          } else {
            this.num += j + "\n";
          }
        }
        lineobj.value = this.num;
        this.num = "";
      }
    },
    //重写a标签锚点跳转
    jump(target) {
      let el = document.querySelector(".el-main");
      let tar = document.querySelector(target);
      el.scrollTop = tar.offsetTop;
    },
  },
  mounted() {
    //textarea自动显示行数
    this.handleTextareaInput();
  },
};
</script>

