<template>
  <el-container id="learning">
    <!-- 目录 -->
    <div class="category">
      <h2>目录</h2>
      <div class="contents">
        <h3>
          <a href="javascript:;" @click="jump('#concepts')">一.相关概念</a>
        </h3>
        <h5>
          <a
            href="javascript:;"
            @click="jump('#concepts1')"
            style="margin-left: 13px; font-size: 13px"
            >1.概述</a
          >
        </h5>
        <h5>
          <a
            href="javascript:;"
            @click="jump('#concepts2')"
            style="margin-left: 13px; font-size: 13px"
            >2.危害</a
          >
        </h5>
        <h3>
          <a href="javascript:;" @click="jump('#methods')">二.解决方案</a>
        </h3>
        <h3>
          <a href="javascript:;" @click="jump('#try')">三.代码实例</a>
        </h3>
      </div>
    </div>
    <!-- 文章内容 -->
    <el-main class="main">
      <h1
        style="
          font-size: 35px;
          font-weight: bold;
          text-align: center;
          background-color: rgb(191, 198, 192);
          color: rgb(73, 71, 70);
          line-height: 80px;
          border-left: 10px solid rgb(74, 147, 74);
        "
      >
        XXE - 外部实体类注入
      </h1>
      <div class="passage1">
        <h1 id="concepts">相关概念</h1>
        <br />
        <h3 id="concepts1" class="contenthead">概述</h3>
        <el-row>
          <el-col :span="24" class="concepts1">
            &nbsp&nbsp&nbsp&nbspXXE为xml外部实体注入的简称。当应用程序在解析XML输入时，在没有禁止外部实体的加载而导致加载了外部文件及代码时，就会造成XXE漏洞。XXE漏洞可以通过file协议或是FTP协议来读取文件源码，当然也可以通过XXE漏洞来对内网进行探测或者攻击
          </el-col>
        </el-row>
        <br />
        <h3 id="concepts2" class="contenthead">危害</h3>
        <el-row>
          <el-col :span="24">
            &nbsp&nbsp&nbsp&nbsp任意文件读取，内网探测，攻击内网站点，命令执行，DOS攻击等
          </el-col>
        </el-row>
      </div>
      <div class="passage2">
        <h1 id="methods">编码建议</h1>
        <br />
        <el-row>
          <el-col>
            &nbsp&nbsp&nbsp&nbsp对于XXE漏洞的防御比较简单，只需在使用XML解析器时设置其属性，禁用DTD或者禁止使用外部实体，一般常见的修复方案如下所示：<br />
            &nbsp&nbsp&nbsp&nbsp//实列化解析类之后通常会支持三个配置<br />
            &nbsp&nbsp&nbsp&nbsp&nbsp&nbsp&nbsp&nbspobj.setFeature("http://apache.org/xml/features/disallow-doctype-deel",true);<br />
            &nbsp&nbsp&nbsp&nbsp&nbsp&nbsp&nbsp&nbspobj.setFeature("http://xml.org/sax/features/external-general-entities",false);<br />
            &nbsp&nbsp&nbsp&nbsp&nbsp&nbsp&nbsp&nbspobj.setFeature("http://xml.org/sax/features/external-parameter-entities",false);<br />
          </el-col>
        </el-row>
      </div>
      <div class="passage3">
        <h1 id="try">代码实例</h1>
        <h3 class="contenthead">
          <i class="el-icon-warning-outline"></i>漏洞代码 XMLReader
        </h3>
        <el-row class="bigger">
          <el-col
            >&nbsp&nbsp&nbsp&nbspXMLReaer接口是一种通过回调读取XML文档的接口，其存在于公共区域中.
            XMReader接口是XML解析器实现SAX2驱动程序所必需的接口，其允许应用程序设置和查询解析器中的功能和属性、
            注册文档处理的事件处理程序以及开始文档解析。
            当XMLReader使用默认的解析方法并且末对XML进行过滤时，会出现XXE漏洞。</el-col
          >
        </el-row>
        <div class="try">
          <div class="tryhead">
            <span class="mark"
              ><div style="background-color: rgb(252, 98, 93)"></div>
              <div style="background-color: rgb(253, 188, 64)"></div>
              <div style="background-color: rgb(53, 205, 75)"></div
            ></span>
            <span style="color: rgb(144, 164, 174)" class="language">java</span>
          </div>
          <div class="code">
            <div class="colshow">
              <textarea
                id="colshow"
                class="rowshow"
                warp="off"
                rows="15"
                disabled
              ></textarea>
            </div>
            <textarea
              id="code"
              class="codeshow"
              rows="15"
              disabled
              onscroll="document.getElementsByClassName('rowshow')[0].scrollTop = this.scrollTop;"
              v-model="code1"
            ></textarea>
          </div>
        </div>
        <br />
        <h3 class="conetenthead">
          <i class="el-icon-warning-outline"></i>漏洞代码 SAXBuilder<br />
        </h3>
        <el-row class="bigger">
          <el-col :span="24">
            &nbsp&nbsp&nbsp&nbspSAXBuider是一个JDOM解析器，其能够将路径中的
            XML文件解析入Doumon对象，SAXBuilder使用第三方SAX解析器来处理解析任务，并使用SAXHandnler的实例侦听SAX事件。当SAXBuilder使用默认的解析方法并且未对XML进行过滤时，会出现XXE漏洞。
            <div class="try">
              <div class="tryhead">
                <span class="mark"
                  ><div style="background-color: rgb(252, 98, 93)"></div>
                  <div style="background-color: rgb(253, 188, 64)"></div>
                  <div style="background-color: rgb(53, 205, 75)"></div
                ></span>
                <span style="color: rgb(144, 164, 174)" class="language"
                  >java</span
                >
              </div>
              <div class="code">
                <div class="colshow">
                  <textarea
                    id="colshow"
                    class="rowshow"
                    warp="off"
                    rows="15"
                    disabled
                  ></textarea>
                </div>
                <textarea
                  id="code"
                  class="codeshow"
                  rows="15"
                  disabled
                  onscroll="document.getElementsByClassName('rowshow')[1].scrollTop = this.scrollTop;"
                  v-model="code2"
                ></textarea>
              </div>
            </div>
          </el-col>
        </el-row>
        <br />
        <h3 class="contenthead">
          <i class="el-icon-warning-outline"></i>漏洞代码 SAXReadr<br />
        </h3>
        <el-row class="bigger">
          <el-col>
            &nbsp&nbsp&nbsp&nbspDOM4J是dom4j.org出品的一个开源xML解析包，
            使用起来非常简单， 只要了解基本的XML-DOM模型， 就能使用。DOM4J
            读/写XML文档主要依赖于org.dom4j.io包，
            它有DOMReader和SAXReadeR两种方式。因为使用了同一个接口，
            所以这两种方式的调用方法是完全致的。同样的，在使用默认解析方法并且未对
            XML进行过滤时，其也会出现XXE漏洞。
            <div class="try">
              <div class="tryhead">
                <span class="mark"
                  ><div style="background-color: rgb(252, 98, 93)"></div>
                  <div style="background-color: rgb(253, 188, 64)"></div>
                  <div style="background-color: rgb(53, 205, 75)"></div
                ></span>
                <span style="color: rgb(144, 164, 174)" class="language"
                  >java</span
                >
              </div>
              <div class="code">
                <div class="colshow">
                  <textarea
                    id="colshow"
                    class="rowshow"
                    warp="off"
                    rows="15"
                    disabled
                  ></textarea>
                </div>
                <textarea
                  id="code"
                  class="codeshow"
                  rows="15"
                  disabled
                  onscroll="document.getElementsByClassName('rowshow')[2].scrollTop = this.scrollTop;"
                  v-model="code3"
                ></textarea>
              </div>
            </div>
          </el-col>
        </el-row>
        <br />
        <h3 class="contenthead">
          <i class="el-icon-warning-outline"></i>漏洞代码 Digestey<br />
        </h3>
        <el-row class="bigger">
          <el-col>
            &nbsp&nbsp&nbsp&nbspDigester类用来将XML映射成Java类，以简化XML的处理。单是Apache
            Commons库中的一个jar包:
            common-digester包。一样的在默认配置下会出现XXE漏洞。其触发
            的XXE漏洞是没有回显的，我们一般需通过Blind XXE的方法来利用:
            <div class="try">
              <div class="tryhead">
                <span class="mark"
                  ><div style="background-color: rgb(252, 98, 93)"></div>
                  <div style="background-color: rgb(253, 188, 64)"></div>
                  <div style="background-color: rgb(53, 205, 75)"></div
                ></span>
                <span style="color: rgb(144, 164, 174)" class="language"
                  >java</span
                >
              </div>
              <div class="code">
                <div class="colshow">
                  <textarea
                    id="colshow"
                    class="rowshow"
                    warp="off"
                    rows="15"
                    disabled
                  ></textarea>
                </div>
                <textarea
                  id="code"
                  class="codeshow"
                  rows="15"
                  disabled
                  onscroll="document.getElementsByClassName('rowshow')[3].scrollTop = this.scrollTop;"
                  v-model="code4"
                ></textarea>
              </div>
            </div>
          </el-col>
        </el-row>
      </div>
    </el-main>
    <div class="menu"></div>
  </el-container>
</template>

