<template>
  <el-container id="learning">
    <!-- 目录 -->
    <div class="category">
      <h2>目录</h2>
      <div class="contents">
        <h3>
          <a href="javascript:;" @click="jump('#concepts')">一.相关概念</a>
        </h3>
        <h5>
          <a
            href="javascript:;"
            @click="jump('#concepts1')"
            style="margin-left: 13px; font-size: 13px"
            >1.概述</a
          >
        </h5>
        <h5>
          <a
            href="javascript:;"
            @click="jump('#concepts2')"
            style="margin-left: 13px; font-size: 13px"
            >2.漏洞原理</a
          >
        </h5>
        <h5>
          <a
            href="javascript:;"
            @click="jump('#concepts3')"
            style="margin-left: 20px; font-size: 8px"
            >2.1端口探测</a
          >
        </h5>
        <h5>
          <a
            href="javascript:;"
            @click="jump('#concepts4')"
            style="margin-left: 20px; font-size: 8px"
            >2.2任意文件读取/下载</a
          >
        </h5>
        <h3>
          <a href="javascript:;" @click="jump('#methods')">二.解决方案</a>
        </h3>
      </div>
    </div>
    <!-- 文章内容 -->
    <el-main class="main">
      <h1
        style="
          font-size: 35px;
          font-weight: bold;
          text-align: center;
          background-color: rgb(191, 198, 192);
          color: rgb(73, 71, 70);
          line-height: 80px;
          border-left: 10px solid rgb(74, 147, 74);
        "
      >
        SSRF - 服务器端请求伪造
      </h1>
      <div class="passage1">
        <h1 id="concepts">相关概念</h1>
        <br />
        <h3 id="concepts1" class="contenthead">概述</h3>
        <el-row class="bigger">
          <el-col :span="24" class="concepts1">
            &nbsp&nbsp&nbsp&nbsp服务端请求伪造（Server-Side Request Forge）简称
            SSRF，是OWASP
            TOP之一，它是由攻击者构造的payload传给服务端，服务端执行后造成了漏洞，一般用于在外网探测或攻击内网服务。Java网络请求支持的协议很多，包括：http，https，file，ftp，mailto，jar，netdoc。如下图所示：
            <img src="./media/show1.png" alt="" style="width: 100%" />
          </el-col>
        </el-row>
        <br />
        <h3 id="concepts2" class="contenthead">漏洞原理</h3>
        <br />
        <el-row id="concepts3">
          <h5 class="contenthead">1 端口探测</h5>
          <el-col :span="24">
            <div class="try">
              <div class="tryhead">
                <span class="mark"
                  ><div style="background-color: rgb(252, 98, 93)"></div>
                  <div style="background-color: rgb(253, 188, 64)"></div>
                  <div style="background-color: rgb(53, 205, 75)"></div
                ></span>
                <span style="color: rgb(144, 164, 174)" class="language"
                  >JAVA</span
                >
              </div>
              <div class="code">
                <div class="colshow">
                  <textarea
                    id="colshow"
                    class="rowshow"
                    warp="off"
                    rows="15"
                    disabled
                  ></textarea>
                </div>
                <textarea
                  id="code"
                  class="codeshow"
                  rows="15"
                  disabled
                  onscroll="document.getElementsByClassName('rowshow')[0].scrollTop = this.scrollTop;"
                  v-model="code1"
                ></textarea>
              </div>
            </div>
            &nbsp&nbsp&nbsp&nbsp以上代码大致意义如下：<br />
            &nbsp&nbsp&nbsp&nbsp&nbsp&nbsp&nbsp&nbspURL对象用openconnection()打开连接，获得URLConnection类对象。<br />
            &nbsp&nbsp&nbsp&nbsp&nbsp&nbsp&nbsp&nbsp用InputStream()获取字节流<br />
            &nbsp&nbsp&nbsp&nbsp&nbsp&nbsp&nbsp&nbsp然后InputStreamReader()将字节流转化成字符流<br />
            &nbsp&nbsp&nbsp&nbsp&nbsp&nbsp&nbsp&nbspBufferedReader()将字符流以缓存形式输出的方式来快速获取网络数据流<br />
            &nbsp&nbsp&nbsp&nbsp&nbsp&nbsp&nbsp&nbsp最终一行一行的输入到 html
            变量中，输出到浏览器<br />
            因为本机地址存在 SSRF
            漏洞，那么久可以利用该漏洞去探测虚拟机开放的端口，如下图所示：
            <img src="./media/show2.png" alt="" style="width: 100%" />
            &nbsp&nbsp&nbsp&nbsp如果该端口没有开放 http/https 协议，那么返回：
            <img src="./media/show3.png" alt="" style="width: 100%" />
            &nbsp&nbsp&nbsp&nbsp根据不同的返回结果，就可以判断开放的 http/https
            端口
          </el-col>
          <h5 class="contenthead">2 任意文件读取/下载</h5>
          <el-col :span="24">
            &nbsp&nbsp&nbsp&nbsp我们将上述代码删除一行，如下：
            <div class="try" id="concepts4">
              <div class="tryhead">
                <span class="mark"
                  ><div style="background-color: rgb(252, 98, 93)"></div>
                  <div style="background-color: rgb(253, 188, 64)"></div>
                  <div style="background-color: rgb(53, 205, 75)"></div
                ></span>
                <span style="color: rgb(144, 164, 174)" class="language"
                  >JAVA</span
                >
              </div>
              <div class="code">
                <div class="colshow">
                  <textarea
                    id="colshow"
                    class="rowshow"
                    warp="off"
                    rows="15"
                    disabled
                  ></textarea>
                </div>
                <textarea
                  id="code"
                  class="codeshow"
                  rows="15"
                  disabled
                  onscroll="document.getElementsByClassName('rowshow')[1].scrollTop = this.scrollTop;"
                  v-model="code2"
                ></textarea>
              </div>
            </div>
            &nbsp&nbsp&nbsp&nbspHttpURLconnection()是基于http协议的，而我们要用的是
            file 协议，删除后即可利用file协议去读取任意文件 ，如下图所示：
            <img src="./media/show4.png" alt="" style="width: 100%" />
            &nbsp&nbsp&nbsp&nbsp如果我们知道了网站的路径，可以直接读取其数据库连接的相关信息：
            <img src="./media/show5.png" alt="" style="width: 100%" />
            &nbsp&nbsp&nbsp&nbsp
            将获取的内容写入到SsrfFileDownTest.txt文件中，测试如下：
            <img src="./media/show6.png" alt="" style="width: 100%" />
          </el-col>
        </el-row>
      </div>
      <div class="passage2">
        <h1 id="methods">编码建议</h1>
        <br />
        <el-row>
          <el-col>
            &nbsp&nbsp&nbsp&nbsp实际场景中可能出现 SSRF
            的功能点有很多,比如获取远程 URL
            图片、webmail收取其他邮箱邮件、从远程服务器请求资源等等，针对这些问题，可以进行过滤判断，设置白名单等，相关策略如下：
            &nbsp&nbsp&nbsp&nbsp&nbsp&nbsp&nbsp&nbsp统一错误信息，避免用户可以根据错误信息来判断远端服务器的端口状态。<br />
            &nbsp&nbsp&nbsp&nbsp&nbsp&nbsp&nbsp&nbsp限制请求的端口为http常用的端口，比如，80,443,8080,8090等。<br />
            &nbsp&nbsp&nbsp&nbsp&nbsp&nbsp&nbsp&nbsp禁用不需要的协议，仅仅允许http和https请求。<br />
            &nbsp&nbsp&nbsp&nbsp&nbsp&nbsp&nbsp&nbsp根据业务需求，判定所需的域名是否是常用的几个，若是，将这几个特定的域名加入到白名单，拒绝白名单域名之外的请求，。<br />
            &nbsp&nbsp&nbsp&nbsp&nbsp&nbsp&nbsp&nbsp根据请求来源，判定请求地址是否是固定请求来源，若是，将这几个特定的域名/IP加入到白名单，拒绝白名单域名/IP之外的请求<br />
            &nbsp&nbsp&nbsp&nbsp&nbsp&nbsp&nbsp&nbsp若业务需求和请求来源并非固定，那么可以自己写一个
            ssrfCheck 函数。
            <br />
          </el-col>
        </el-row>
      </div>
    </el-main>
    <div class="menu"></div>
  </el-container>
</template>

<script>
export default {
  name: "Learning",
  data() {
    return {
      num: "",
      code1: `			String url = request.getParameter("url");
			String htmlContent;
			try {
				URL u = new URL(url);
				URLConnection urlConnection = u.openConnection();
				HttpURLConnection httpUrl = (HttpURLConnection) urlConnection;
				BufferedReader base = new BufferedReader(new InputStreamReader(httpUrl.getInputStream(), "UTF-8"));
				StringBuffer html = new StringBuffer();
				while ((htmlContent = base.readLine()) != null) {
					html.append(htmlContent);
				}
				base.close();
				print.println("<b>端口探测</b></br>");
				print.println("<b>url:" + url + "</b></br>");
				print.println(html.toString());
				print.flush();
			} catch (Exception e) {
				e.printStackTrace();
				print.println("ERROR!");
				print.flush();
			}`,
      code2: `String url = request.getParameter("url");
		String htmlContent;

		try {
			URL u = new URL(url);
			URLConnection urlConnection = u.openConnection();
			BufferedReader base = new BufferedReader(new InputStreamReader(urlConnection.getInputStream()));
			StringBuffer html = new StringBuffer();
			while ((htmlContent = base.readLine()) != null) {
				html.append(htmlContent);
			}
			base.close();
			print.println(html.toString());
			print.flush();

		} catch (Exception e) {
			e.printStackTrace();
			print.println("ERROR!");
			print.flush();
		}`,
    };
  },
  methods: {
    //textarea自动显示行数
    handleTextareaInput() {
      var target = document.getElementsByClassName("codeshow");
      for (let i = 0; i < target.length; i++) {
        var str = target[i].value;
        str = str.replace(/\r/gi, "");
        str = str.split("\n");
        var n = str.length;
        var lineobj = document.getElementsByClassName("rowshow")[i];
        for (var j = 1; j <= n; j++) {
          if (document.all) {
            this.num += j + "\r\n"; //判断浏览器是否是IE
          } else {
            this.num += j + "\n";
          }
        }
        lineobj.value = this.num;
        this.num = "";
      }
    },
    //重写a标签锚点跳转
    jump(target) {
      let el = document.querySelector(".el-main");
      let tar = document.querySelector(target);
      el.scrollTop = tar.offsetTop;
    },
  },
  mounted() {
    //textarea自动显示行数
    this.handleTextareaInput();
  },
};
</script>

