<template>
  <el-container direction="vertical">
    <el-header>
      <div class="avatar">
        <el-avatar :size="90" :src="url"></el-avatar>
        <span class="username">{{ username }}</span>
      </div>
      <i class="el-icon-arrow-left" @click="goBack">返回审计界面</i>
    </el-header>
    <el-main>
      <table>
        <tr>
          <th>审计类型</th>
          <th>审计时间</th>
          <th>操作</th>
        </tr>
        <tr v-for="(item, index) in auditHistory" class="body">
          <td style="width: 30%">{{ item.type }}</td>
          <td style="width: 30%">
            {{ item.sendTime[0] }}/{{ item.sendTime[1] }}/{{
              item.sendTime[2]
            }}&nbsp&nbsp&nbsp&nbsp{{ item.sendTime[3] }}:{{
              item.sendTime[4]
            }}:{{ item.sendTime[5] }}
          </td>
          <td style="width: 40%">
            <el-button type="success" @click="goAudit"
              ><i class="el-icon-view"></i>查看</el-button
            >
            <!-- <router-link
              tag="a"
              target="_blank"
              :to="{ name: learning[item.sort] }"
              ><el-button type="primary"
                ><i class="el-icon-message-solid">学习</i></el-button
              ></router-link
            > -->
          </td>
        </tr>
      </table>
    </el-main>
    <div class="hand"></div>
    <div class="head"></div>
  </el-container>
</template>

<script>
import { mapState } from "vuex";
export default {
  name: "Information",
  data() {
    return {
      url: require("./media/default.png"),
      username: "",
      history: [],
    };
  },
  methods: {
    goBack() {
      this.$router.push("/audit");
    },
    goAudit() {
      this.$router.push("/audit");
    },
  },
  computed: {
    ...mapState({
      auditHistory: (state) => state.info.history,
    }),
  },
  watch: {
    history: {
      deep: true,
      handler() {
        this.history = this.aduitHistory;
      },
    },
  },
  created() {
    this.username = window.localStorage.getItem("username");
    this.$store.dispatch("getHistory");
  },
};
</script>

<style lang="css" scoped>
body,
html {
  overflow: hidden;
}

.el-container {
  height: 100%;
  width: 100%;
  background-color: #202021;
  font-size: 14px;
  color: #dddddd;
  overflow: hidden;
  position: relative;
  overflow: hidden;
}
/* 用户信息样式开始 */
.el-header {
  position: relative;
  margin: 20px 50px !important;
  margin-top: 20px;
  height: 200px !important;
  background: url("./media/bg.png");
  opacity: 0.95;
  z-index: 999;
}

.el-header .el-avatar {
  position: absolute;
  top: 20px;
}

.el-header .username {
  position: absolute;
  top: 54px;
  left: 140px;
  font-size: 20px;
}

.el-header .el-icon-arrow-left {
  position: absolute;
  bottom: 2px;
  font-size: 20px;
  color: rgb(192, 138, 62);
  cursor: pointer;
}

.el-header .el-icon-edit {
  position: absolute;
  bottom: 34px;
  left: 24px;
  font-size: 20px;
  color: rgb(192, 138, 62);
  cursor: pointer;
}
/* 用户信息样式结束 */

/* 审计记录样式开始 */
.el-main {
  padding: 0 50px;
  opacity: 0.8;
  z-index: 999;
}

.el-main table {
  width: 100%;
  background-color: rgb(255, 255, 255);
  z-index: 999;
}

.el-main table th {
  color: rgb(51, 51, 51);
}

.el-main table td {
  color: rgb(122, 122, 122);
  text-align: center;
}

.el-main table tr {
  height: 60px;
}

.el-main table .body:hover {
  background-color: rgb(245, 247, 250);
}

.el-main table .body .el-button {
  margin-left: 70px;
}

/* 审计记录样式结束 */

/* 动画样式开始 */
@keyframes movelevel {
  0% {
    transform: translateX(20%);
  }
  100% {
    transform: translateX(0);
  }
}

@keyframes movevertical {
  0% {
    transform: translateY(20%);
  }
  100% {
    transform: translateY(0);
  }
}

.hand {
  position: absolute;
  width: 400px;
  height: 400px;
  background: url("./media/hand.png") no-repeat;
  background-size: 400px 400px;
  bottom: 0px;
  left: -10px;
  animation: movevertical 4s;
}

.head {
  position: absolute;
  width: 400px;
  height: 400px;
  background: url("./media/head.png") center center;
  background-size: 400px 400px;
  top: -200px;
  right: -100px;
  animation: movelevel 4s;
}

/* 动画样式结束 */
</style>