<template>
  <el-container id="learning">
    <!-- 目录 -->
    <div class="category">
      <h2>目录</h2>
      <div class="contents">
        <h3>
          <a href="javascript:;" @click="jump('#concepts')">一.相关概念</a>
        </h3>
        <h5>
          <a
            href="javascript:;"
            @click="jump('#concepts1')"
            style="margin-left: 13px; font-size: 13px"
            >1.概述</a
          >
        </h5>
        <h5>
          <a
            href="javascript:;"
            @click="jump('#concepts2')"
            style="margin-left: 13px; font-size: 13px"
            >2.前提</a
          >
        </h5>
        <h5>
          <a
            href="javascript:;"
            @click="jump('#concepts3')"
            style="margin-left: 13px; font-size: 13px"
            >3.挖掘过程</a
          >
        </h5>
        <h5>
          <a
            href="javascript:;"
            @click="jump('#concepts4')"
            style="margin-left: 13px; font-size: 13px"
            >4.挖掘技巧</a
          >
        </h5>
        <h3>
          <a href="javascript:;" @click="jump('#methods')">二.解决方案</a>
        </h3>
      </div>
    </div>
    <!-- 文章内容 -->
    <el-main class="main">
      <h1
        style="
          font-size: 35px;
          font-weight: bold;
          text-align: center;
          background-color: rgb(191, 198, 192);
          color: rgb(73, 71, 70);
          line-height: 80px;
          border-left: 10px solid rgb(74, 147, 74);
        "
      >
        反序列化
      </h1>
      <div class="passage1">
        <h1 id="concepts">相关概念</h1>
        <br />
        <h3 id="concepts1" class="contenthead">概述</h3>
        <el-row>
          <el-col :span="24" class="concepts1" style="line-height: 25px">
            &nbsp&nbsp&nbsp&nbsp序列化是让 Java 对象脱离 Java
            运行环境的一种手段，可以有效的实现多平台之间的通信、对象持久化存储。只有实现了
            Serializable 和 Externalizable 接口的类的对象才能被序列化。 Java
            程序使用 ObjectInputStream 对象的 readObject
            方法将反序列化数据转换为 java
            对象。但当输入的反序列化的数据可被用户控制，那么攻击者即可通过构造恶意输入，让反序列化产生非预期的对象，在此过程中执行构造的任意代码。
          </el-col>
        </el-row>
        <br />
        <h3 id="concepts2" class="contenthead">前提</h3>
        <el-row class="bigger">
          <el-col :span="24">
            &nbsp&nbsp&nbsp&nbsp一个类的对象要想序列化成功，必须满足两个条件：<br />
            &nbsp&nbsp&nbsp&nbsp&nbsp&nbsp&nbsp&nbsp1.该类必须实现
            java.io.Serializable 接口。<br />
            &nbsp&nbsp&nbsp&nbsp&nbsp&nbsp&nbsp&nbsp2.该类的所有属性必须是可序列化的。如果有一个属性不是可序列化的，则该属性必须注明是短暂的。
          </el-col>
        </el-row>
        <h3 id="concepts3" class="contenthead">挖掘过程</h3>
        <el-row class="bigger">
          <el-col :span="24">
            &nbsp&nbsp&nbsp&nbsp输入序列化后的字节码对象，使得达到延迟5秒载入页面的效果（RCE），可以看到序列化的数据的特征都是以rO0A开头，现实开发里一般是先将对象序列化处理传输，再经过反序列化过程还原，这里省略了前者。<br />
            &nbsp&nbsp&nbsp&nbsp同样抓包定位到源码，走读代码：
            <div class="try">
              <div class="tryhead">
                <span class="mark"
                  ><div style="background-color: rgb(252, 98, 93)"></div>
                  <div style="background-color: rgb(253, 188, 64)"></div>
                  <div style="background-color: rgb(53, 205, 75)"></div
                ></span>
                <span style="color: rgb(144, 164, 174)" class="language"></span>
              </div>
              <div class="code">
                <div class="colshow">
                  <textarea
                    id="colshow"
                    class="rowshow"
                    warp="off"
                    rows="15"
                    disabled
                  ></textarea>
                </div>
                <textarea
                  id="code"
                  class="codeshow"
                  rows="15"
                  disabled
                  onscroll="document.getElementsByClassName('rowshow')[0].scrollTop = this.scrollTop;"
                  v-model="code1"
                ></textarea>
              </div>
            </div>
            &nbsp&nbsp&nbsp&nbsp代码逻辑是对输入字符串b64token先进行base64解码，然后使用原生的ObjectInputStream的readObject()方法进行反序列化操作。<br />

            &nbsp&nbsp&nbsp&nbsp确定存在漏洞后可使用BurpSuite的插件进行扫描，插件地址：
            https://github.com/federicodotta/Java-Deserialization-Scanner/
            <br />
            &nbsp&nbsp&nbsp&nbsp当然也可以自写使用ObjectOutputStream序列化一个Runtime.getRuntime()的数据进行远程代码执行。详细过程可阅读原文，参考国外一篇文章《Java
            Deserialization — From Discovery to Reverse Shell on Limited
            Environments》。
          </el-col>
        </el-row>
        <h3 id="concepts4" class="contenthead">挖掘技巧</h3>
        <el-row class="bigger">
          <el-col :span="24">
            &nbsp&nbsp&nbsp&nbsp&nbsp挖掘反序列化漏洞在业务功能层面关注导入模版文件、网络通信、数据传输、日志格式化存储、对象数据、磁盘或DB存储等场景。<br />
            &nbsp&nbsp&nbsp&nbsp&nbsp代码层需重点关注一些反序列化操作函数，定位代码后判断反序列化数据是否可控，定位入口需关注反序列化常用的类和函数：
            <div class="try">
              <div class="tryhead">
                <span class="mark"
                  ><div style="background-color: rgb(252, 98, 93)"></div>
                  <div style="background-color: rgb(253, 188, 64)"></div>
                  <div style="background-color: rgb(53, 205, 75)"></div
                ></span>
                <span style="color: rgb(144, 164, 174)" class="language"></span>
              </div>
              <div class="code">
                <div class="colshow">
                  <textarea
                    id="colshow"
                    class="rowshow"
                    warp="off"
                    rows="15"
                    disabled
                  ></textarea>
                </div>
                <textarea
                  id="code"
                  class="codeshow"
                  rows="15"
                  disabled
                  onscroll="document.getElementsByClassName('rowshow')[1].scrollTop = this.scrollTop;"
                  v-model="code2"
                ></textarea>
              </div>
            </div>
            &nbsp&nbsp&nbsp&nbsp&nbsp当确定可以带入序列化数据的入口后，则寻找POP
            Gadgets利用链，一些存在危险的基础库和框架就提供了可导致命令执行 POP
            链的环境，使用ysoserial可直接生成对应的序列化数据对接口进行漏洞利用。<br />
            &nbsp&nbsp&nbsp&nbsp&nbsp
            所以在代码审计前可优先查看pom.xml文件，分析是否出现漏洞组件，如CommonsBeanutils、Fastjson<1.2.47等，存在危险的基础库有：
            <div class="try">
              <div class="tryhead">
                <span class="mark"
                  ><div style="background-color: rgb(252, 98, 93)"></div>
                  <div style="background-color: rgb(253, 188, 64)"></div>
                  <div style="background-color: rgb(53, 205, 75)"></div
                ></span>
                <span style="color: rgb(144, 164, 174)" class="language"></span>
              </div>
              <div class="code">
                <div class="colshow">
                  <textarea
                    id="colshow"
                    class="rowshow"
                    warp="off"
                    rows="15"
                    disabled
                  ></textarea>
                </div>
                <textarea
                  id="code"
                  class="codeshow"
                  rows="15"
                  disabled
                  onscroll="document.getElementsByClassName('rowshow')[2].scrollTop = this.scrollTop;"
                  v-model="code3"
                ></textarea>
              </div>
            </div>
          </el-col>
        </el-row>
      </div>
      <div class="passage2">
        <h1 id="methods">编码建议</h1>
        <br />
        <el-row style="line-height: 25px">
          <el-col>
            &nbsp&nbsp&nbsp&nbsp升级服务端所依赖的可能被利用的jar包，包括JDK。<br />
            &nbsp&nbsp&nbsp&nbsp在执行反序列前对InputStream对象进行检查过滤，推荐一个开源的Java反序列化库SerialKiller：https://github.com/ikkisoft/SerialKiller
          </el-col>
        </el-row>
      </div>
    </el-main>
    <div class="menu"></div>
  </el-container>
</template>

<script>
export default {
  name: "Learning",
  data() {
    return {
      num: "",
      code1: `@ResponseBody
    public AttackResultcompleted(@RequestParam String token) throws IOException {
        String b64token;
        long before, after;
        int delay;
        
        b64token =token.replace('-', '+').replace('_', '/');
        try (ObjectInputStreamois = new ObjectInputStream(newByteArrayInputStream(Base64.getDecoder().decode(b64token)))) {
             before = System.currentTimeMillis(); 
             Object o = ois.readObject();`,
      code2: `ObjectInputStream.readObject
ObjectInputStream.readUnshared
XMLDecoder.readObject
Yaml.load
XStream.fromXML
ObjectMapper.readValue
JSON.parseObject
Serializable`,
      code3: `commons-io 2.4
commons-collections 3.1
commons-logging 1.2
commons-beanutils 1.9.2
org.slf4j:slf4j-api 1.7.21
com.mchange:mchange-commons-java 0.2.11
org.apache.commons:commons-collections 4.0
com.mchange:c3p0 0.9.5.2
org.beanshell:bsh 2.0b5
org.codehaus.groovy:groovy 2.3.9
org.springframework:spring-aop4.1.4.RELEASE`,
    };
  },
  methods: {
    //textarea自动显示行数
    handleTextareaInput() {
      var target = document.getElementsByClassName("codeshow");
      for (let i = 0; i < target.length; i++) {
        var str = target[i].value;
        str = str.replace(/\r/gi, "");
        str = str.split("\n");
        var n = str.length;
        var lineobj = document.getElementsByClassName("rowshow")[i];
        for (var j = 1; j <= n; j++) {
          if (document.all) {
            this.num += j + "\r\n"; //判断浏览器是否是IE
          } else {
            this.num += j + "\n";
          }
        }
        lineobj.value = this.num;
        this.num = "";
      }
    },
    //重写a标签锚点跳转
    jump(target) {
      let el = document.querySelector(".el-main");
      let tar = document.querySelector(target);
      el.scrollTop = tar.offsetTop;
    },
  },
  mounted() {
    //textarea自动显示行数
    this.handleTextareaInput();
  },
};
</script>

