    <!-- 文章内容 -->
    <el-main class="main">
      <h1
        style="
          font-size: 35px;
          font-weight: bold;
          text-align: center;
          background-color: rgb(191, 198, 192);
          color: rgb(73, 71, 70);
          line-height: 80px;
          border-left: 10px solid rgb(74, 147, 74);
        "
      >
        Sql注入 - JDBC
      </h1>
      <div class="passage1">
        <h1 id="concepts">相关概念</h1>
        <br />
        <h3 id="concepts1" class="contenthead">概述</h3>
        <el-row>
          <el-col :span="24" class="concepts1">
            &nbsp&nbsp&nbsp&nbsp所谓 SQL 注入，就是通过将 SQL 命令插入应用程序的
            http
            请求中，并在服务器端被接收后用于参与数据库操作，最终达到欺骗服务器执行恶意的
            SQL 命令的效果，Java 的 SQL 注入和 PHP 中的 SQL
            注入，其实差别不大，理论上来讲，应用程序中只要是与数据库存在数据交互，无论是增删改查，只要传入的数据完全受用户控制，且应用程序对用户传入的数据没有进行妥当的处理，那么这些地方都是可能存在
            SQL 注入的。
          </el-col>
        </el-row>
        <br />
        <h3 id="concepts2" class="contenthead">什么是JDBC</h3>
        <el-row>
          <el-col :span="24">
            &nbsp&nbsp&nbsp&nbspJDBC（Java Database
            Connectivity,java数据库连接）是一种用于执行SQL语句的Java
            API，可以为多种关系数据库提供统一访问，它由一组用Java语言编写的类和接口组成。JDBC提供了一种基准，据此可以构建更高级的工具和接口，使数据库开发人员能够编写数据库应用程序。<br />
            &nbsp&nbsp&nbsp&nbsp我们安装好数据库之后，我们的应用程序也是不能直接使用数据库的，必须要通过相应的数据库驱动程序，通过驱动程序去和数据库打交道。其实也就是数据库厂商的JDBC接口实现，即对Connection等接口的实现类的jar文件。
          </el-col>
        </el-row>
        <h3 id="concepts3" class="contenthead">常用接口</h3>
        <el-row>
          <el-col :span="24" style="line-height: 25px">
            1.Driver接口<br />
            &nbsp&nbsp&nbsp&nbspDriver接口由数据库厂家提供，作为java开发人员，只需要使用Driver接口就可以了。在编程中要连接数据库，必须先装载特定厂商的数据库驱动程序，不同的数据库有不同的装载方法。如：<br />
            装载MySql驱动：Class.forName("com.mysql.jdbc.Driver");<br />装载Oracle驱动：Class.forName("oracle.jdbc.driver.OracleDriver");<br />
            2.Connection接口<br />
            &nbsp&nbsp&nbsp&nbspConnection与特定数据库的连接（会话）,在连接上下文中执行sql语句并返回结果。DriverManager.getConnection(url,user,
            password)方法建立在JDBC URL中定义的数据库Connection连接上。<br />
            &nbsp&nbsp&nbsp&nbsp常用方法：<br />
            &nbsp&nbsp&nbsp&nbsp&nbsp&nbsp&nbsp&nbspcreateStatement()：创建向数据库发送sql的statement对象。<br />
            &nbsp&nbsp&nbsp&nbsp&nbsp&nbsp&nbsp&nbspprepareStatement(sql)
            ：创建向数据库发送预编译sql的PrepareSatement对象。<br />
            &nbsp&nbsp&nbsp&nbsp&nbsp&nbsp&nbsp&nbspprepareCall(sql)：创建执行存储过程的callableStatement对象。<br />
            &nbsp&nbsp&nbsp&nbsp&nbsp&nbsp&nbsp&nbspsetAutoCommit(boolean
            autoCommit)：设置事务是否自动提交。<br />
            &nbsp&nbsp&nbsp&nbsp&nbsp&nbsp&nbsp&nbspcommit()
            ：在链接上提交事务。<br />
            &nbsp&nbsp&nbsp&nbsp&nbsp&nbsp&nbsp&nbsprollback()
            ：在此链接上回滚事务。<br />
            3.Statement接口<br />
            &nbsp&nbsp&nbsp&nbsp
            用于执行静态SQL语句并返回它所生成结果的对象。<br />
            &nbsp&nbsp&nbsp&nbsp三种Statement类：<br />
            &nbsp&nbsp&nbsp&nbsp&nbsp&nbsp&nbsp&nbspStatement：由createStatement创建，用于发送简单的SQL语句（不带参数）。<br />
            &nbsp&nbsp&nbsp&nbsp&nbsp&nbsp&nbsp&nbspPreparedStatement
            ：继承自Statement接口，由preparedStatement创建，用于发送含有一个或多个参数的SQL语句。PreparedStatement对象比Statement对象的效率更高，并且可以防止SQL注入，所以我们一般都使用PreparedStatement。<br />
            &nbsp&nbsp&nbsp&nbsp&nbsp&nbsp&nbsp&nbspCallableStatement：继承自PreparedStatement接口，由方法prepareCall创建，用于调用存储过程。<br />
            &nbsp&nbsp&nbsp&nbsp常用Statement方法：<br />
            &nbsp&nbsp&nbsp&nbsp&nbsp&nbsp&nbsp&nbspexecute(String
            sql):运行语句，返回是否有结果集<br />
            &nbsp&nbsp&nbsp&nbsp&nbsp&nbsp&nbsp&nbspexecuteQuery(String
            sql)：运行select语句，返回ResultSet结果集。<br />
            &nbsp&nbsp&nbsp&nbsp&nbsp&nbsp&nbsp&nbspexecuteUpdate(String
            sql)：运行insert/update/delete操作，返回更新的行数。<br />
            &nbsp&nbsp&nbsp&nbsp&nbsp&nbsp&nbsp&nbspaddBatch(String sql)
            ：把多条sql语句放到一个批处理中。<br />
            &nbsp&nbsp&nbsp&nbsp&nbsp&nbsp&nbsp&nbspexecuteBatch()：向数据库发送一批sql语句执行。<br />
            4.ResultSet接口<br />
            &nbsp&nbsp&nbsp&nbspResultSet提供检索不同类型字段的方法，常用的有：<br />
            &nbsp&nbsp&nbsp&nbsp&nbsp&nbsp&nbsp&nbspgetString(int
            index)、getString(String
            columnName)：获得在数据库里是varchar、char等类型的数据对象。<br />
            &nbsp&nbsp&nbsp&nbsp&nbsp&nbsp&nbsp&nbspgetFloat(int
            index)、getFloat(String
            columnName)：获得在数据库里是Float类型的数据对象。<br />
            &nbsp&nbsp&nbsp&nbsp&nbsp&nbsp&nbsp&nbspgetDate(int
            index)、getDate(String
            columnName)：获得在数据库里是Date类型的数据。<br />
            &nbsp&nbsp&nbsp&nbsp&nbsp&nbsp&nbsp&nbspgetBoolean(int
            index)、getBoolean(String
            columnName)：获得在数据库里是Boolean类型的数据。<br />
            &nbsp&nbsp&nbsp&nbsp&nbsp&nbsp&nbsp&nbspgetObject(int
            index)、getObject(String
            columnName)：获取在数据库里任意类型的数据。<br />
            &nbsp&nbsp&nbsp&nbspResultSet还提供了对结果集进行滚动的方法：<br />
            &nbsp&nbsp&nbsp&nbsp&nbsp&nbsp&nbsp&nbspnext()：移动到下一行
            (注意：初始时，指针指向第一行的前一行，通过resultset.next()方法将游标移动到下一行，返回值是一个布尔类型。）<br />
            &nbsp&nbsp&nbsp&nbsp&nbsp&nbsp&nbsp&nbspPrevious()：移动到前一行<br />
            &nbsp&nbsp&nbsp&nbsp&nbsp&nbsp&nbsp&nbspabsolute(int
            row)：移动到指定行<br />
            &nbsp&nbsp&nbsp&nbsp&nbsp&nbsp&nbsp&nbspbeforeFirst()：移动resultSet的最前面。<br />
            &nbsp&nbsp&nbsp&nbsp&nbsp&nbsp&nbsp&nbspafterLast()
            ：移动到resultSet的最后面。<br />
            &nbsp&nbsp&nbsp&nbsp&nbsp使用后依次关闭对象及连接：ResultSet →
            Statement → Connection <br />
          </el-col>
        </el-row>
      </div>
      <div class="passage2">
        <h1 id="methods">编码建议</h1>
        <br />
        <el-row>
          <el-col style="line-height: 25px">
            1.PreparedStatement <br />
            &nbsp&nbsp&nbsp&nbsp采用预编译语句集，它内置了处理SQL注入的能力，只要使用它的setXXX方法传值即可。<br />
            &nbsp&nbsp&nbsp&nbsp优点: <br />
            &nbsp&nbsp&nbsp&nbsp&nbsp&nbsp&nbsp&nbsp(1).代码的可读性和可维护性.<br />
            &nbsp&nbsp&nbsp&nbsp&nbsp&nbsp&nbsp&nbsp(2).PreparedStatement尽最大可能提高性能.<br />
            &nbsp&nbsp&nbsp&nbsp&nbsp&nbsp&nbsp&nbsp(3).最重要的一点是极大地提高了安全性.<br />
            2.使用正则表达式过滤传入的参数<br />
            &nbsp&nbsp&nbsp&nbsp要引入的包：<br />
            &nbsp&nbsp&nbsp&nbsp &nbsp&nbsp&nbsp&nbspimport
            java.util.regex.*;<br />
            &nbsp&nbsp&nbsp&nbsp正则表达式：<br />
            &nbsp&nbsp&nbsp&nbsp &nbsp&nbsp&nbsp&nbspprivate String CHECKSQL =
            “^(.+)\\sand\\s(.+)|(.+)\\sor(.+)\\s$”;<br />
            &nbsp&nbsp&nbsp&nbsp判断是否匹配：<br />
            &nbsp&nbsp&nbsp&nbsp
            &nbsp&nbsp&nbsp&nbspPattern.matches(CHECKSQL,targerStr);<br />
          </el-col>
        </el-row>
      </div>
      <div class="passage3">
        <h1 id="try">代码实例</h1>
        <div class="try">
          <div class="contenthead">
            <i class="el-icon-warning-outline"></i>漏洞代码 - 拼接SQL语句
