<script>
import { mapState } from "vuex";
import { reqGetDanger1, reqGetSafe, reqGetDanger2 } from "../../../api/index";
export default {
  name: "JDBC",
  data() {
    return {
      num: "",
      code1: `@GetMapping("/study/native")
public R<String> jdbcVul(String id){
    StringBuilder result= new StringBuilder();
    try {
        Class.forName("com.mysql.cj.jdbc.Driver");

        Connection connection = DriverManager.getConnection(dbUrl, dbUsername, dbPassword);

        Statement statement = connection.createStatement();
        String sql="select * from test where id='"+id+"'";
        log.info("[vul]执行SQL语句："+sql);
        ResultSet rs=statement.executeQuery(sql);

        //获取查询结果
        while(rs.next()){
            String username = rs.getString("username");
            result.append(String.format("查询结果 %s ",username));
        }
        rs.close();;
        connection.close();
    } catch (Exception e) {
        e.printStackTrace();
    }
    //把查询结果返回给前台
    return R.success(result.toString());
}`,
      code2: `@GetMapping("/study/pre")
public  R<String> preVul(String id){
    StringBuilder result = new StringBuilder();
    try {
        Class.forName("com.mysql.cj.jdbc.Driver");

        Connection connection = DriverManager.getConnection(dbUrl, dbUsername, dbPassword);

        String sql="select * from test where id = ?";
        PreparedStatement preparedStatement=connection.prepareStatement(sql);
        preparedStatement.setString(1,id);//通过预编译传值
        ResultSet rs = preparedStatement.executeQuery();
        while (rs.next()){
            String username = rs.getString("username");
            result.append(String.format("查询结果 %s ",username));
        }
    } catch (Exception e) {
        e.printStackTrace();
    }
    return R.success(result.toString());
}`,
      code3: `@GetMapping("/study/preErr")
public R<String>   preErr(String id){
    StringBuilder result = new StringBuilder();
    try {
        Class.forName("com.mysql.cj.jdbc.Driver");
        Connection connection = DriverManager.getConnection(dbUrl, dbUsername, dbPassword);
        String sql = "select * from test where id = " + id;
        PreparedStatement st = connection.prepareStatement(sql);
        log.info("[preErr] 执行SQL语句：" + st);
        ResultSet rs = st.executeQuery();
        while (rs.next()){
            String username = rs.getString("username");
            result.append(String.format("查询结果 %s ",username));
        }
    } catch (Exception e) {
        e.printStackTrace();
    }
    return R.success(result.toString());
}`,
    };
  },
  methods: {
    //textarea自动显示行数
    handleTextareaInput() {
      var target = document.getElementsByClassName("codeshow");
      for (let i = 0; i < target.length; i++) {
        var str = target[i].value;
        str = str.replace(/\r/gi, "");
        str = str.split("\n");
        var n = str.length;
        var lineobj = document.getElementsByClassName("rowshow")[i];
        for (var j = 1; j <= n; j++) {
          if (document.all) {
            this.num += j + "\r\n"; //判断浏览器是否是IE
          } else {
            this.num += j + "\n";
          }
        }
        lineobj.value = this.num;
        this.num = "";
      }
    },
    //重写a标签锚点跳转
    jump(target) {
      let el = document.querySelector(".el-main");
      let tar = document.querySelector(target);
      el.scrollTop = tar.offsetTop;
    },
    reset1() {
      let tar = document.getElementsByClassName("codeshow")[0];
      tar.value = this.code1;
      this.handleTextareaInput();
    },
    coderun1() {
      let tar = document.getElementsByClassName("codeshow")[0];
      tar.value = this.code1rsl;
      this.handleTextareaInput();
    },
    reset2() {
      let tar = document.getElementsByClassName("codeshow")[1];
      tar.value = this.code2;
      this.handleTextareaInput();
    },
    coderun2() {
      let tar = document.getElementsByClassName("codeshow")[1];
      tar.value = this.code2rsl;
      this.handleTextareaInput();
    },
    reset3() {
      let tar = document.getElementsByClassName("codeshow")[2];
      tar.value = this.code3;
      this.handleTextareaInput();
    },
    coderun3() {
      let tar = document.getElementsByClassName("codeshow")[2];
      tar.value = this.code3rsl;
      this.handleTextareaInput();
    },
  },
  computed: {
    ...mapState({
      code1rsl: (state) => state.learning.danger1,
      code2rsl: (state) => state.learning.safe,
      code3rsl: (state) => state.learning.danger2,
    }),
  },
  mounted() {
    this.$store.dispatch("getResult");
    let codeshow1 = document.getElementsByClassName("codeshow1")[0];
    codeshow1.value = this.code1;
    let codeshow2 = document.getElementsByClassName("codeshow2")[0];
    codeshow2.value = this.code2;
    let codeshow3 = document.getElementsByClassName("codeshow3")[0];
    codeshow3.value = this.code3;
    //textarea自动显示行数
    this.handleTextareaInput();
  },
};
</script>

