//按需引入element-ui
import {
    Button, Container, Aside, Avatar, Header, Icon, Tabs, TabPane, Menu, Submenu, MenuItem, MenuItemGroup, Row,
    Col, PageHeader, Main, Drawer, Input, Table, TableColumn, Dialog, Empty, Message
} from 'element-ui'

//封装引入
const element = {
    install(Vue) {
        Vue.use(Button);
        Vue.use(Container);
        Vue.use(Aside);
        Vue.use(Avatar);
        Vue.use(Main);
        Vue.use(Header);
        Vue.use(Icon);
        Vue.use(Tabs);
        Vue.use(TabPane);
        Vue.use(Menu);
        Vue.use(Submenu);
        Vue.use(MenuItem);
        Vue.use(MenuItemGroup);
        Vue.use(Row);
        Vue.use(Col);
        Vue.use(PageHeader);
        Vue.use(Drawer);
        Vue.use(Input);
        Vue.use(Table);
        Vue.use(TableColumn);
        Vue.use(Dialog);
        Vue.use(Empty);
        //注册$message方法（Vue方法）
        Vue.prototype.$message = Message
        //注册$router方法（Vue-router方法）
    }
}

//对外暴露封装函数
export default element;