 和 Hibernate 的替代方案。MyBatis 内部封装了 JDBC，简化了加载驱动、创建连接、创建 statement 等繁杂的过程，开发者只需要关注 SQL 语句本身。"),_c('br'),_vm._v("     MyBatis 支持定制化 SQL、存储过程以及高级映射，可以在实体类和 SQL 语句之间建立映射关系，是一种半自动化的 ORM 实现。其封装性低于 Hibernate，但性能优秀、小巧、简单易学、应用广泛。"),_c('br'),_vm._v("     Mybatis官方文档     https://mybatis.org/mybatis-3/zh/ ")])],1),_c('br'),_c('h3',{staticClass:"contenthead",attrs:{"id":"concepts3"}},[_vm._v("mybatis如何防止sql注入")]),_c('el-row',{staticStyle:{"line-height":"25px"}},[_c('el-col',{attrs:{"span":24}},[_vm._v("     MyBatis框架作为一款半自动化的持久层框架，其SQL语句都要我们自己手动编写，这个时候当然需要防止SQL注入。其实，MyBatis的SQL是一个具有“输入+输出”的功能，类似于函数的结构，其中，parameterType表示了输入的参数类型，resultType表示了输出的参数类型。回应上文，如果我们想防止SQL注入，理所当然地要在输入参数上下功夫。上面代码中使用#的即输入参数在SQL中拼接的部分，传入参数后，打印出执行的SQL语句，会看到SQL是这样的: "),_c('br'),_vm._v("         "),_c('i',{staticStyle:{"font-weight":"800"}},[_vm._v("select id, username, password, role from user where username=? and password=?")]),_c('br'),_vm._v("     不管输入什么参数，打印出的SQL都是这样的。这是因为MyBatis启用了预编译功能，在SQL执行前，会先将上面的SQL发送给数据库进行编译；执行时，直接使用编译好的SQL，替换占位符“?”就可以了。因为SQL注入只能对编译过程起作用，所以这样的方式就很好地避免了SQL注入的问题。"),_c('br'),_vm._v("     【底层实现原理】MyBatis是如何做到SQL预编译的呢？其实在框架底层，是JDBC中的PreparedStatement类在起作用，PreparedStatement是我们很熟悉的Statement的子类，它的对象包含了编译好的SQL语句。这种“准备好”的方式不仅能提高安全性，而且在多次执行同一个SQL时，能够提高效率。原因是SQL已编译好，再次执行时无需再编译。"),_c('br'),_c('div',{staticClass:"try"},[_c('div',{staticClass:"tryhead"},[_c('span',{staticClass:"mark"},[_c('div',{staticStyle:{"background-color":"rgb(252, 98, 93)"}}),_c('div',{staticStyle:{"background-color":"rgb(253, 188, 64)"}}),_c('div',{staticStyle:{"background-color":"rgb(53, 205, 75)"}})]),_c('span',{staticClass:"language",staticStyle:{"color":"rgb(144, 164, 174)"}},[_vm._v("java")])]),_c('div',{staticClass:"code"},[_c('div',{staticClass:"colshow"},[_c('textarea',{staticClass:"rowshow",attrs:{"id":"colshow","warp":"off","rows":"15","disabled":""}})]),_c('textarea',{directives:[{name:"model",rawName:"v-model",value:(_vm.code1),expression:"code1"}],staticClass:"codeshow",attrs:{"id":"code","rows":"15","disabled":