<template>
  <el-container id="learning">
    <!-- 目录 -->
    <div class="category">
      <h2>目录</h2>
      <div class="contents">
        <h3>
          <a href="javascript:;" @click="jump('#concepts')">一.相关概念</a>
        </h3>
        <h5>
          <a
            href="javascript:;"
            @click="jump('#concepts1')"
            style="margin-left: 13px; font-size: 13px"
            >1.概述</a
          >
        </h5>
        <h5>
          <a
            href="javascript:;"
            @click="jump('#concepts2')"
            style="margin-left: 13px; font-size: 13px"
            >2.XSS类型</a
          >
        </h5>
        <h5>
          <a
            href="javascript:;"
            @click="jump('#concepts3')"
            style="margin-left: 20px; font-size: 8px"
            >2.1反射型XSS</a
          >
        </h5>
        <h5>
          <a
            href="javascript:;"
            @click="jump('#concepts4')"
            style="margin-left: 20px; font-size: 8px"
            >2.存储型XSS</a
          >
        </h5>
        <h3>
          <a href="javascript:;" @click="jump('#methods')">二.解决方案</a>
        </h3>
      </div>
    </div>
    <!-- 文章内容 -->
    <el-main class="main">
      <h1
        style="
          font-size: 35px;
          font-weight: bold;
          text-align: center;
          background-color: rgb(191, 198, 192);
          color: rgb(73, 71, 70);
          line-height: 80px;
          border-left: 10px solid rgb(74, 147, 74);
        "
      >
        XSS - 跨站脚本攻击
      </h1>
      <div class="passage1">
        <h1 id="concepts">相关概念</h1>
        <br />
        <h3 id="concepts1" class="contenthead">概述</h3>
        <el-row class="bigger">
          <el-col :span="24" class="concepts1">
            &nbsp&nbsp&nbsp&nbsp跨站脚本攻击XSS(Cross Site
            Scripting)，为了不和层叠样式表(Cascading Style Sheets,
            CSS)的缩写混淆，故将跨站脚本攻击缩写为XSS。恶意攻击者往Web页面里插入恶意Script代码，当用户浏览该页面时，嵌入Web里面的Script代码会被执行，从而达到恶意攻击用户的目的。XSS攻击针对的是用户层面的攻击。
          </el-col>
        </el-row>
        <br />
        <h3 id="concepts2" class="contenthead bigger">XSS类型</h3>
        <el-col :span="24">
          &nbsp&nbsp&nbsp&nbspXSS 是通过对网页插入可执行代码且成功地被浏览器
          执行，达到攻击的目的，一般来说 XSS 的危害性没有 SQL 大，但是一次有效的
          XSS 攻击可以做很多事情，比如获取
          Cookies、获取用户的联系人列表、截屏、劫持等等。根据服务端的后端代码不同，XSS
          的种类也不相同，一般可以分为反射型、存储型以及和反射型相近的 DOM
          型。</el-col
        >
        <h5 class="contenthead" id="concepts3">1 反射型XSS</h5>
        <el-row class="bigger">
          <el-col :span="24">
            &nbsp&nbsp&nbsp&nbsp1.拿上方下载的代码举例，在com.sec.servlet包下的
            InfoServlet.java文件中，关键代码如下：
            <div class="try">
              <div class="tryhead">
                <span class="mark"
                  ><div style="background-color: rgb(252, 98, 93)"></div>
                  <div style="background-color: rgb(253, 188, 64)"></div>
                  <div style="background-color: rgb(53, 205, 75)"></div
                ></span>
                <span style="color: rgb(144, 164, 174)" class="language"
                  >JAVA</span
                >
              </div>
              <div class="code">
                <div class="colshow">
                  <textarea
                    id="colshow"
                    class="rowshow"
                    warp="off"
                    rows="15"
                    disabled
                  ></textarea>
                </div>
                <textarea
                  id="code"
                  class="codeshow"
                  rows="15"
                  disabled
                  onscroll="document.getElementsByClassName('rowshow')[0].scrollTop = this.scrollTop;"
                  v-model="code1"
                ></textarea>
              </div>
            </div>
            &nbsp&nbsp&nbsp&nbsp获取
            msg字段，然后直接打印出来。这里就可以很直白的理解反射型 XSS
            了，把你输入的东西给”原封不动“的返回给你，这里的原封不动当然是加引号的，如果在输入的内容中，插入了浏览器可以执行的
            js 代码，那么就会导致这种反射型的 XSS。<br />

            &nbsp&nbsp&nbsp&nbsp如下图所示，是上述代码的功能界面：<br />
            <img src="./media/show1.png" alt="" style="width: 100%" />
            &nbsp&nbsp&nbsp&nbsp当我们输入正常字符的时候，返回我们刚才输入的字符：<br />
            <img src="./media/show2.png" alt="" style="width: 100%" />
            &nbsp&nbsp&nbsp&nbsp但是如果我们输入的内容中含有可执行代码，如：<img
              src="./media/show4.png"
              alt=""
            />
            <img src="./media/show3.png" alt="" style="width: 100%" />
            &nbsp&nbsp&nbsp&nbsp浏览器就会执行这段
            js代码，所以我们只要控制输入的内容，就可以达到攻击效果。
          </el-col>
        </el-row>
        <h5 class="contenthead" id="concepts4">2 存储型XSS</h5>
        <el-row class="bigger">
          <el-col :span="24">
            &nbsp&nbsp&nbsp&nbsp同样拿上方下载的代码举例，在com.sec.servlet包下的
            ShowServlet.java文件中，关键代码如下：
            <div class="try">
              <div class="tryhead">
                <span class="mark"
                  ><div style="background-color: rgb(252, 98, 93)"></div>
                  <div style="background-color: rgb(253, 188, 64)"></div>
                  <div style="background-color: rgb(53, 205, 75)"></div
                ></span>
                <span style="color: rgb(144, 164, 174)" class="language"
                  >JAVA</span
                >
              </div>
              <div class="code">
                <div class="colshow">
                  <textarea
                    id="colshow"
                    class="rowshow"
                    warp="off"
                    rows="15"
                    disabled
                  ></textarea>
                </div>
                <textarea
                  id="code"
                  class="codeshow"
                  rows="15"
                  disabled
                  onscroll="document.getElementsByClassName('rowshow')[1].scrollTop = this.scrollTop;"
                  v-model="code2"
                ></textarea>
              </div>
            </div>
            &nbsp&nbsp&nbsp&nbsp其中，MessageInfoShowService主要是用于实例化MessageInfoDaoImpl()，然后调用MessageInfoShowDao()类，该类内容如下：
            <div class="try">
              <div class="tryhead">
                <span class="mark"
                  ><div style="background-color: rgb(252, 98, 93)"></div>
                  <div style="background-color: rgb(253, 188, 64)"></div>
                  <div style="background-color: rgb(53, 205, 75)"></div
                ></span>
                <span style="color: rgb(144, 164, 174)" class="language"
                  >JAVA</span
                >
              </div>
              <div class="code">
                <div class="colshow">
                  <textarea
                    id="colshow"
                    class="rowshow"
                    warp="off"
                    rows="15"
                    disabled
                  ></textarea>
                </div>
                <textarea
                  id="code"
                  class="codeshow"
                  rows="15"
                  disabled
                  onscroll="document.getElementsByClassName('rowshow')[2].scrollTop = this.scrollTop;"
                  v-model="code3"
                ></textarea>
              </div>
            </div>
            &nbsp&nbsp&nbsp&nbsp主要执行的是从message表中查询所有数据，然后将
            name、mail、message 的值加到 messageinfo List 中，最后返回给 servlet
            层。 这段代码中有地址转发，在message.jsp中存在以下内容：
            <div class="try">
              <div class="tryhead">
                <span class="mark"
                  ><div style="background-color: rgb(252, 98, 93)"></div>
                  <div style="background-color: rgb(253, 188, 64)"></div>
                  <div style="background-color: rgb(53, 205, 75)"></div
                ></span>
                <span style="color: rgb(144, 164, 174)" class="language"
                  >JAVA</span
                >
              </div>
              <div class="code">
                <div class="colshow">
                  <textarea
                    id="colshow"
                    class="rowshow"
                    warp="off"
                    rows="15"
                    disabled
                  ></textarea>
                </div>
                <textarea
                  id="code"
                  class="codeshow"
                  rows="15"
                  disabled
                  onscroll="document.getElementsByClassName('rowshow')[3].scrollTop = this.scrollTop;"
                  v-model="code4"
                ></textarea>
              </div>
            </div>
            &nbsp&nbsp&nbsp&nbsp从 messageinfo List 中取出 name、mail、message
            的值，并输出在该页面上。<br />
            &nbsp&nbsp&nbsp&nbsp这样一来整个流程就很清楚了，从 message
            表中取数据--> 取出的数据输出到页面上<br />
            &nbsp&nbsp&nbsp&nbsp那么这里就存在一个问题，如果储存的数据有问题，存在可执行代码，那么输出到页面上的内容就会引起xss
            漏洞。<br />
            &nbsp&nbsp&nbsp&nbsp继续看代码，找可以控制输入点的地方，在com.sec.servlet包下的
            StoreServlet.java文件中，关键代码如下：<br />
            &nbsp&nbsp&nbsp&nbsp获取 name、mail、message
            参数，然后传入到MessageInfoStoreService()类中，该类的主要作用是调用MessageInfoStoreDao()类，该类的关键内容如下：
            <div class="try">
              <div class="tryhead">
                <span class="mark"
                  ><div style="background-color: rgb(252, 98, 93)"></div>
                  <div style="background-color: rgb(253, 188, 64)"></div>
                  <div style="background-color: rgb(53, 205, 75)"></div
                ></span>
                <span style="color: rgb(144, 164, 174)" class="language"
                  >JAVA</span
                >
              </div>
              <div class="code">
                <div class="colshow">
                  <textarea
                    id="colshow"
                    class="rowshow"
                    warp="off"
                    rows="15"
                    disabled
                  ></textarea>
                </div>
                <textarea
                  id="code"
                  class="codeshow"
                  rows="15"
                  disabled
                  onscroll="document.getElementsByClassName('rowshow')[4].scrollTop = this.scrollTop;"
                  v-model="code5"
                ></textarea>
              </div>
            </div>
            &nbsp&nbsp&nbsp&nbsp主要执行的是向数据库中插入数据的功能，数据插入之前虽然进行了预编译，但是没有进行特殊字符过滤处理，这样结合前文中提到的——直接输出从message
            表中拿出的数据，导致了储存型 XSS 漏洞。<br />

            &nbsp&nbsp&nbsp&nbsp如下图，我们提交含有可执行代码的数据：
            <img src="./media/show5.png" style="width: 100%" />
            &nbsp&nbsp&nbsp&nbsp然后在输出页面查看：
            <img src="./media/show6.png" style="width: 100%" />
            &nbsp&nbsp&nbsp&nbsp成功执行
            XSS，且点击确定后，返回原本页面，再次刷新
            <img src="./media/show7.png" style="width: 100%" />
            <img src="./media/show8.png" style="width: 100%" />
            &nbsp&nbsp&nbsp&nbsp依旧会执行插入数据中的 XSS
            可执行代码，这也是和反射型 XSS 最大的区别。
          </el-col>
        </el-row>
      </div>
      <div class="passage2">
        <h1 id="methods">编码建议</h1>
        <br />
        <el-row>
          <el-col>
            &nbsp&nbsp&nbsp&nbsp 对于
            XSS漏洞，导致其产生的根本原因是对于输入和输出功能的过滤不完善，因此可以采用过滤的方法来防御XSS漏洞，大致方向有以下几种：<br />
            &nbsp&nbsp&nbsp&nbsp &nbsp&nbsp&nbsp&nbsp
            保留语意，将输入的特殊字符转译存储到数据库，缺点是可能会对数据库或文件系统产生一些不必要的垃圾信息<br />
            &nbsp&nbsp&nbsp&nbsp &nbsp&nbsp&nbsp&nbsp
            过滤掉特殊字符，只保留正常数据，缺点是有些时候用户需要输入特殊字符，不能保证数据原始性<br />
            &nbsp&nbsp&nbsp&nbsp &nbsp&nbsp&nbsp&nbsp
            输入限制，含有特殊字符的数据不能够输入<br />
            &nbsp&nbsp&nbsp&nbsp &nbsp&nbsp&nbsp&nbsp
            以上都可以自行进行特殊处理，这里只提供些思路，怎么处理可以根据需求选择<br />
            &nbsp&nbsp&nbsp&nbsp 这里提供几个具体的处理方式:<br />
            &nbsp&nbsp&nbsp&nbsp &nbsp&nbsp&nbsp&nbsp1、全局过滤器过滤<br />
            &nbsp&nbsp&nbsp&nbsp &nbsp&nbsp&nbsp&nbsp2、使用工具类xssProtect
            <br />
            &nbsp&nbsp&nbsp&nbsp &nbsp&nbsp&nbsp&nbsp3、commons.lang包 <br />
          </el-col>
        </el-row>
      </div>
    </el-main>
    <div class="menu"></div>
  </el-container>
</template>

<script>
export default {
  name: "Traversal",
  data() {
    return {
      num: "",
      code1: `public void Message(HttpServletRequest req, HttpServletResponse resp) {
		// TODO Auto-generated method stub
		String message = req.getParameter("msg");
		
		try {
			resp.getWriter().print(message);
			
		} catch (IOException e) {
			// TODO Auto-generated catch block
			e.printStackTrace();
		}
	
	}
	`,
      code2: `public void ShowMessage(HttpServletRequest req, HttpServletResponse resp) throws ServletException, IOException {
		// TODO Auto-generated method stub
		
		MessageInfoService msginfo = new MessageInfoServiceImpl();
		
		List<MessageInfo> msg = msginfo.MessageInfoShowService();
		if( msg != null){
			req.setAttribute("msg", msg);
			req.getRequestDispatcher("/message.jsp").forward(req, resp);
			return ;
		}
	}`,
      code3: `try {
			....
        
			String sql = "select * from message";
			ps = conns.prepareStatement(sql);
			rs = ps.executeQuery();
			messageinfo = new ArrayList<MessageInfo>();
			while(rs.next()){
				MessageInfo msg = new MessageInfo();
				
				msg.setName(rs.getString("name"));
				msg.setMail(rs.getString("mail"));
				msg.setMessage(rs.getString("message"));
				
				messageinfo.add(msg);
			}
  
		....
      
		return messageinfo;
	}
}`,
      code4: `<%
	List<MessageInfo> msginfo = (ArrayList<MessageInfo>)request.getAttribute("msg");
	for(MessageInfo m:msginfo){
 %>
<table>

	<tr><td class="klytd"> 留言人：</td>
		<td class ="hvttd">　<%=m.getName() %></td>
	</tr> 
	<tr><td class="klytd"> e-mail：</td><td class ="hvttd">　<%=m.getMail() %></td>
		</tr>
	<tr><td class="klytd"> 内容：</td><td class ="hvttd">　<%=m.getMessage() %></td></tr>

</table> <% } %>
</div>`,
      code5: `try {
  	
  	  boolean result = false;
			....
        
			String sql = "INSERT INTO message (name,mail,message) VALUES (?,?,?)";
			ps = conn.prepareStatement(sql);
			ps.setString(1, name);
			ps.setString(2, mail);
			ps.setString(3, message);
			ps.execute();
			result = true;
			
		....
		
		return result;
	}
      `,
    };
  },
  methods: {
    //textarea自动显示行数
    handleTextareaInput() {
      var target = document.getElementsByClassName("codeshow");
      for (let i = 0; i < target.length; i++) {
        var str = target[i].value;
        str = str.replace(/\r/gi, "");
        str = str.split("\n");
        var n = str.length;
        var lineobj = document.getElementsByClassName("rowshow")[i];
        for (var j = 1; j <= n; j++) {
          if (document.all) {
            this.num += j + "\r\n"; //判断浏览器是否是IE
          } else {
            this.num += j + "\n";
          }
        }
        lineobj.value = this.num;
        this.num = "";
      }
    },
    //重写a标签锚点跳转
    jump(target) {
      let el = document.querySelector(".el-main");
      let tar = document.querySelector(target);
      el.scrollTop = tar.offsetTop;
    },
  },
  mounted() {
    //textarea自动显示行数
    this.handleTextareaInput();
  },
};
</script>

