//二次封装axios
import axios from 'axios'

let requests = axios.create({
    timeout: 5000
})

requests.interceptors.request.use((config) => {
    return config;
})

requests.interceptors.response.use((res) => {
    return res.data;
}, (err) => {
    alert("服务器响应数据失败");
})

export default requests;