            style="margin-left: 13px; font-size: 13px"
            >2.Mybatis</a
          >
        </h5>
        <h5>
          <a
            href="javascript:;"
            @click="jump('#concepts3')"
            style="margin-left: 13px; font-size: 13px"
            >3.防范原理</a
          >
        </h5>
        <h3>
          <a href="javascript:;" @click="jump('#methods')">二.编码建议</a>
        </h3>
      </div>
    </div>
    <!-- 文章内容 -->
    <el-main class="main">
      <h1
        style="
          font-size: 35px;
          font-weight: bold;
          text-align: center;
          background-color: rgb(191, 198, 192);
          color: rgb(73, 71, 70);
          line-height: 80px;
          border-left: 10px solid rgb(74, 147, 74);
        "
      >
        Sql注入 - Mybatis
      </h1>
      <div class="passage1">
        <h1 id="concepts">相关概念</h1>
        <br />
        <h3 id="concepts1" class="contenthead">概述</h3>
        <el-row>
          <el-col :span="24" class="concepts1">
            &nbsp&nbsp&nbsp&nbsp所谓 SQL 注入，就是通过将 SQL 命令插入应用程序的
            http
            请求中，并在服务器端被接收后用于参与数据库操作，最终达到欺骗服务器执行恶意的
            SQL 命令的效果，Java 的 SQL 注入和 PHP 中的 SQL
            注入，其实差别不大，理论上来讲，应用程序中只要是与数据库存在数据交互，无论是增删改查，只要传入的数据完全受用户控制，且应用程序对用户传入的数据没有进行妥当的处理，那么这些地方都是可能存在
            SQL 注入的。
          </el-col>
        </el-row>
        <br />
        <h3 id="concepts2" class="contenthead">什么是Mybatis</h3>
        <el-row>
          <el-col :span="24">
            &nbsp&nbsp&nbsp&nbspMyBatis 是一个开源、轻量级的数据持久化框架，是
            JDBC 和 Hibernate 的替代方案。MyBatis 内部封装了
            JDBC，简化了加载驱动、创建连接、创建 statement
            等繁杂的过程，开发者只需要关注 SQL 语句本身。<br />
            &nbsp&nbsp&nbsp&nbspMyBatis 支持定制化
            SQL、存储过程以及高级映射，可以在实体类和 SQL
            语句之间建立映射关系，是一种半自动化的 ORM 实现。其封装性低于
            Hibernate，但性能优秀、小巧、简单易学、应用广泛。<br />
            &nbsp&nbsp&nbsp&nbspMybatis官方文档
            &nbsp&nbsp&nbsp&nbsphttps://mybatis.org/mybatis-3/zh/
          </el-col>
        </el-row>
        <br />
        <h3 id="concepts3" class="contenthead">mybatis如何防止sql注入</h3>
        <el-row style="line-height: 25px">
          <el-col :span="24">
            &nbsp&nbsp&nbsp&nbspMyBatis框架作为一款半自动化的持久层框架，其SQL语句都要我们自己手动编写，这个时候当然需要防止SQL注入。其实，MyBatis的SQL是一个具有“输入+输出”的功能，类似于函数的结构，其中，parameterType表示了输入的参数类型，resultType表示了输出的参数类型。回应上文，如果我们想防止SQL注入，理所当然地要在输入参数上下功夫。上面代码中使用#的即输入参数在SQL中拼接的部分，传入参数后，打印出执行的SQL语句，会看到SQL是这样的:
            <br />
            &nbsp&nbsp&nbsp&nbsp&nbsp&nbsp&nbsp&nbsp<i style="font-weight: 800"
              >select id, username, password, role from user where username=?
              and password=?</i
            ><br />
            &nbsp&nbsp&nbsp&nbsp不管输入什么参数，打印出的SQL都是这样的。这是因为MyBatis启用了预编译功能，在SQL执行前，会先将上面的SQL发送给数据库进行编译；执行时，直接使用编译好的SQL，替换占位符“?”就可以了。因为SQL注入只能对编译过程起作用，所以这样的方式就很好地避免了SQL注入的问题。<br />
            &nbsp&nbsp&nbsp&nbsp【底层实现原理】MyBatis是如何做到SQL预编译的呢？其实在框架底层，是JDBC中的PreparedStatement类在起作用，PreparedStatement是我们很熟悉的Statement的子类，它的对象包含了编译好的SQL语句。这种“准备好”的方式不仅能提高安全性，而且在多次执行同一个SQL时，能够提高效率。原因是SQL已编译好，再次执行时无需再编译。<br />
            <div class="try">
              <div class="tryhead">
                <span class="mark"
                  ><div style="background-color: rgb(252, 98, 93)"></div>
                  <div style="background-color: rgb(253, 188, 64)"></div>
                  <div style="background-color: rgb(53, 205, 75)"></div
                ></span>
                <span style="color: rgb(144, 164, 174)" class="language"
                  >java</span
                >
              </div>
              <div class="code">
                <div class="colshow">
                  <textarea
                    id="colshow"
                    class="rowshow"
                    warp="off"
                    rows="15"
                    disabled
                  ></textarea>
                </div>
                <textarea
                  id="code"
                  class="codeshow"
                  rows="15"
                  disabled
                  onscroll="document.getElementById('colshow').scrollTop = this.scrollTop;"
                  v-model="code1"
                ></textarea>
              </div>
            </div>
            &nbsp&nbsp&nbsp&nbsp简单说，#{}是经过预编译的，是安全的；${}是未经过预编译的，仅仅是取变量的值，是非安全的，存在SQL注入。<br />
            &nbsp&nbsp&nbsp&nbsp如果我们order
            by语句后用了${}，那么不做任何处理的时候是存在SQL注入危险的。你说怎么防止，那我只能悲惨的告诉你，你得手动处理过滤一下输入的内容。如判断一下输入的参数的长度是否正常（注入语句一般很长），更精确的过滤则可以查询一下输入的参数是否在预期的参数集合中。
          </el-col>
        </el-row>
      </div>
      <div class="passage2">
        <h1 id="methods">编码建议</h1>
        <br />
        <el-row>
          <el-col>
            【必须】sql语句默认使用预编译并绑定变量<br />
            使用mybatis作为持久层框架，应通过#{}语法进行参数绑定,Mybatis会创建PreparedStatement参数占位符,并通过占位符安全地设置参数。
            针对order by注入可以在java层面做映射来进行解决
          </el-col>
        </el-row>
      </div>
    </el-main>
    <div class="menu"></div>
  </el-container>
</template>

<script>
export default {
  name: "JDBC",
  data() {
    return {
      num: "",
      code1:
        "//安全的，预编译了的\nConnection conn = getConn();//获得连接\nString sql = 'select id, username, password, role from user where id=?'; //执行sql前会预编译号该语句\nPreparedStatement pstmt = conn.prepareStatement(sql); \npstmt.setString(1, id); \nResultSet rs=pstmt.executeUpdate(); \n\n\n//不安全的，没进行预编译\nprivate String getNameByUserId(String userId) {\nConnection conn = getConn();//获得连接\nString sql = 'select id,username,password,role from user where id=' + id;\n//当id参数为'3;drop table user;'时，执行的sql语句如下:\n//select id,username,password,role from user where id=3; drop table user;\nPreparedStatement pstmt =  conn.prepareStatement(sql);\nResultSet rs=pstmt.executeUpdate();\n......",
    };
  },
  methods: {
    //textarea自动显示行数
    handleTextareaInput() {
      var target = document.getElementsByClassName("codeshow");
      for (let i = 0; i < target.length; i++) {
        var str = target[i].value;
        str = str.replace(/\r/gi, "");
        str = str.split("\n");
        var n = str.length;
        var lineobj = document.getElementsByClassName("rowshow")[i];
        for (var j = 1; j <= n; j++) {
          if (document.all) {
            this.num += j + "\r\n"; //判断浏览器是否是IE
          } else {
            this.num += j + "\n";
          }
        }
        lineobj.value = this.num;
        this.num = "";
      }
    },
    //重写a标签锚点跳转
    jump(target) {
      let el = document.querySelector(".el-main");
      let tar = document.querySelector(target);
      el.scrollTop = tar.offsetTop;
    },
  },
  mounted() {
    //textarea自动显示行数
    this.handleTextareaInput();
  },
};
</script>

