//引入mock请求
import mockRequests from './mockAjax'
import requests from './Ajax'

//mock请求
export const reqGetAuditBug = () => mockRequests.get('/auditBug'); //audit界面   漏洞原理与解决方案说明


//真实请求
export const reqGetJDBC = (data) => requests({ url: "/jdbc/code", data, method: 'post' }); //jdbc审计结果集
export const reqGetMybatis = (data) => requests({ url: "/mybatis/code", data, method: 'post' });//mybatis审计结果集
export const reqGetUpload = (data) => requests({ url: '/upload/code', data, method: 'post' });//文件上传审计结果集
export const reqGetCross = (data) => requests({ url: '/traversal/code', data, method: 'post' });//路径穿越审计结果集
export const reqGetSSRF = (data) => requests({ url: "/SSRF/code", data, method: "post" });//SSRF审计结果集
export const reqGetXEE = (data) => requests({ url: '/xee/code', data, method: 'post' })//xee审计结果集
export const reqGetRCE = (data) => requests({ url: '/rce/code', data, method: 'post' })//rce审计结果集]
export const reqPostLogup = (data) => requests({ url: "/user/resister", data, headers: { 'Content-Type': 'application/json' }, method: 'post' });//用户注册
export const reqPostLogin = (data) => requests({ url: "/user/login", data, headers: { 'Content-Type': 'application/json' }, method: 'post' });//用户登录
export const reqgetHistory = () => requests({ url: '/result/get', method: 'get' })//用户审计记录
export const reqPostHistory = (data) => requests({ url: "/result/set", data, headers: { 'Content-Type': 'application/json' }, method: 'post' })//保存用户的审计记录
export const reqPostlogout = () => requests({ url: '/user/logout', method: 'post' });//用户注销
export const reqGetDanger1 = () => requests({ url: "/jdbc/study/native?id=1' or '1'='1", method: "get" }) //jdbc危险代码运行结果1
export const reqGetSafe = () => requests({ url: "/jdbc/study/pre?id=1' or '1'='1", method: 'get' })//jdbc安全代码运行结果
export const reqGetDanger2 = () => requests({ url: "/jdbc/study/pre?id='1' or '1'='1'", method: 'get' })//jdbc危险代码运行结果2